import { connect } from 'react-redux'
import {
    documentsDownload, 
    documentsRemove, 
    documentsChangeType,
    documentsMove,
    alertShow } from '../actions'
import DocumentView from '../components/elements/DocumentView'

const mapStateToProps = state => {
    return {
        status: state.documents.fetchStatus,
        folder: state.documents.folder,
        path: state.documents.path,
        current: state.documents.current,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onDownload: file => {
            dispatch(documentsDownload([file]))
        },
        onRemove: file => {
            return dispatch(documentsRemove([file]))
        },
        onMoveFile: (file, path) => {
            dispatch(documentsMove([file], path))
        },
        onChangeType: (file, slug) => {
            dispatch(documentsChangeType(file, slug))
        },
        onAlert: (message, level) => {
            dispatch(alertShow(message, level))
        }
    }
}

const ConnectedDocumentView = connect(
    mapStateToProps,
    mapDispatchToProps
)(DocumentView)

export default ConnectedDocumentView


