import { extractFileAttributes } from '../tools'
import _ from 'lodash'

import { ROOT_PATH, ROOT_NAME } from '../constants'

export const IMPORTER_CLICK = "IMPORTER_CLICK"
export const IMPORTER_FILE_UPLOAD = "IMPORTER_FILE_UPLOAD"
export const IMPORTER_FILE_UPLOAD_SUCESS = "IMPORTER_FILE_UPLOAD_SUCESS"
export const IMPORTER_FILE_UPLOAD_FAILURE = "IMPORTER_FILE_UPLOAD_FAILURE"
export const IMPORTER_DIRECTORY_CHANGE = "IMPORTER_DIRECTORY_CHANGE"
export const IMPORTER_VIEW_CHANGE = "IMPORTER_VIEW_CHANGE"
export const IMPORTER_TREE = "IMPORTER_TREE"
export const IMPORTER_TREE_SUCCESS = "IMPORTER_TREE_SUCCESS"
export const IMPORTER_TREE_FAILURE = "IMPORTER_TREE_FAILURE"

export const importerClick = (path="", slug="") => {
    if(path === "") path = ROOT_PATH
    return (dispatch, getState) => {
        if(_.isEmpty(getState().importer.documentsTree))
            dispatch(importerTree())
        return dispatch({type: IMPORTER_CLICK, slug, path})
    }
}
export const importerDirectoryChange = path => ({type: IMPORTER_DIRECTORY_CHANGE, path})
export const importerViewChange = view => ({type: IMPORTER_VIEW_CHANGE, view})

const findChildren = async(tree) => {
    const result = await cozy.client.files.statByPath(tree.path)
    const next = []
    const childs = result.relations('contents')
    for(var child of childs) {
        if(child.attributes.type === 'directory') {
            const newDir = {
                path: child.attributes.path,
                name: child.attributes.name,
                childs: [],
                dir: true
            }
            next.push(await findChildren(newDir))
            tree.childs.push(newDir)
        } else {
            const newFile = {
                path: result.attributes.path + '/' + child.attributes.name,
                name: child.attributes.name,
            }
            tree.childs.push(newFile)
        }
    }
    return next
}

export const importerTree = () => {
    return async(dispatch, getState) => {
        dispatch({type: IMPORTER_TREE})

        try {
            const tree = {path: ROOT_PATH, childs : [], name: ROOT_NAME.substring(1), dir: true}
            await findChildren(tree)
            return dispatch({type: IMPORTER_TREE_SUCCESS, tree})
        } catch (err) {
            return dispatch({type: IMPORTER_TREE_FAILURE, err})
        }
    }
}

export const importerFilesUpload = (files, dirId) => {
    return async(dispatch, getState) => {
        dispatch({
            type: IMPORTER_FILE_UPLOAD,
            dirId
        })
        const newFiles = []
        const conflits = []
        try {
            for(let data of files) {
                let file
                
                if(data.isDirectory) {

                    try {
                        //create the dir
                        const newDir = await cozy.client.files.createDirectory({
                            name: data.name,
                            dirID: dirId
                        })

                        //add content
                        for(let file of data.content) {
                            try {
                                file = await cozy.client.files.create(file, {
                                    name: file.name,
                                    dirID: newDir._id
                                })
                            } catch (err) {
                                throw err
                            }
                        }

                        newFiles.push(newDir)                        
                    } catch (err) {
                        if(err.status === 409) {
                            console.log('Confict with file', data.name)
                            conflits.push(data)
                        } else {
                            throw err
                        }
                    }

                } else {
                    try {
                        file = await cozy.client.files.create(data, {
                            name: data.name,
                            dirID: dirId
                        })

                        if(data.typeFile) {
                            file = await cozy.client.files.updateAttributesById(file._id, {tags:[data.typeFile]})
                        }
                        newFiles.push(file)

                    } catch (err) {
                        if(err.status === 409) {
                            console.log('Confict with file', data.name)
                            conflits.push(data)
                        } else {
                            throw err
                        }
                    }
                }

            }
            let action = {
                type: IMPORTER_FILE_UPLOAD_SUCESS,
                files: newFiles.map(f => extractFileAttributes(f)),
                conflits
            }
            const currentDir = getState().documents.folder
            if(currentDir){
                action.currentDir = currentDir.id === dirId
            }
            if(conflits.length > 0){
                action.alert = {
                    message: `Plusieurs fichiers n'ont pas était ajouté car ils existaient deja dans ce dossier : \n` + conflits.map(f =>f.name).join('\n')
                }
            }
            return dispatch(action)
        } catch(err) {
            return dispatch({
                type: IMPORTER_FILE_UPLOAD_FAILURE,
                err
            })
        }

    }
}


