import React, {Component} from 'react'
import classNames from 'classnames'
import moment from 'moment'
import style from '../../styles/ElementList.sass'

import { VIEW_GRID, VIEW_LIST, DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_MAINTENANCES, DOCTYPE_HOMEDATA, DOCTYPE_WORKS } from '../../constants'

export const DataList = (props) => {
    return (
        <div className={classNames(style.list, {
            [style.viewList]: props.view === VIEW_LIST,
            [style.viewGrid]: props.view === VIEW_GRID
        })}>
            {props.children.map((item, key) => (
                <div key={key} onClick={e => props.onClickItem(item, e)} className={classNames(style.document, {[style.selected]: item.selected})}>
                    {props.onSelectItem && item.selected && (
                        <i className={classNames(style.checkbox, "ion-android-checkbox-outline")} onClick={(e) => {e.stopPropagation(); props.onSelectItem(item, e);}}></i>
                    )}

                    {props.onSelectItem && !item.selected && (
                        <i className={classNames(style.checkbox, "ion-android-checkbox-outline-blank")} onClick={(e) => {e.stopPropagation(); props.onSelectItem(item, e);}}></i>
                    )}

                    <div className={style.content}>
                        <img
                            src={getItemIcon(item)}
                            onError={e => console.log('Error icon', e)}
                        ></img>
                    </div>

                    <div
                    className={style.name}
                    title={item.type || item.name}>
                        {item.type && item.name ? `${item.type} : ${item.name}` : (item.type ? item.type : item.name)}
                    </div>
                </div> 
            ))}
        </div>
    )
}

const getItemIcon = (item) => {
    let icon = '/images/'

    switch (item._type || item.doctype) {
        case DOCTYPE_DIAGNOSIS:
            icon += 'data-diagnostics'
            break

        case DOCTYPE_HOMEDATA:
            icon += 'data-donnees-de-propriete'
            break

        case DOCTYPE_EQUIPMENTS:
            icon += 'data-equipements'
            break

        case DOCTYPE_MAINTENANCES:
        case DOCTYPE_WORKS:
            icon += 'data-travaux-et-entretien'
            break
        default:
            icon += 'data-element'
    }

    if(item.selected) icon += '.selected'
    return icon + '.png'
}

export default DataList


