import React, {Component} from 'react'
import classNames from 'classnames'
import button from '../../styles/buttons.scss'

export default class RenameInput extends Component {

    constructor(props, context) {
        super(props, context)

        this.closeIntent = null
        this.name = this.props.name

        this.state = {
            newName: this.props.name.replace(/[\/*\\'":|=,\[\]]/, '_')
        }
    }

    cancelRename(name) {
        this.closeIntent = setTimeout(() => {
            this.props.onCancel()
        }, 100)
    }

    stopCancelRename(){
        clearTimeout(this.closeIntent)
    }

    checkNewName(newName) {
        if(!(/[\/*\\'":|=,\[\]]/.test(newName))) {
            this.setState({newName: newName})
        }
    }

    rename() {
        if(this.name === this.state.newName) return this.props.onCancel()

        this.props.onRename(this.state.newName)
    }

    render() {
        return (
            <span style={{display: 'inline-flex'}}>
                <input autoFocus type="text" value={this.state.newName}
                    onChange={(e)=> this.checkNewName(e.target.value)}
                    onKeyPress={(e) => {
                        if(e.key === 'Enter') this.rename()
                    }}
                    style={{'width': (10 + this.state.newName.length * 13.1) + 'px'}}
                />
                <button className={classNames(button.button, button.stable)} onFocus={(e) => this.stopCancelRename()} onClick={() => this.rename()}><i className="ion-ios-checkmark-empty"></i></button>
                <button className={classNames(button.button, button.assertive)} onClick={(e) => this.cancelRename()}><i className="ion-ios-close-empty"></i></button>
            </span>
        )
    }
}


