import React, {Component} from 'react'
import classNames from 'classnames'

import button from '../../styles/buttons.scss'
import style from '../../styles/ConfirmModal.sass'

import Modal from './Modal'

export default class PromptModal extends Component {
    constructor(props) {
        super(props)

        this.state = {
            value: '',
            red: false
        }
    }

    checkAndConfirm() {
        if(this.state.value === '') return this.setState({red: true})

        this.props.confirm(this.state.value)
    }

    render() {
        const {abort} = this.props

        return (
            <Modal onClickOutside={e => abort()}>
                <div className={style.main} onClick={e => e.stopPropagation()}>
                    <div className={style.content}>
                        <h4>Nom du nouveau dossier : </h4>
                        <input type="text" value={this.state.value} onChange={e => this.setState({value: e.target.value})} style={this.state.red && this.state.value === '' ?{outline: '1px auto red'} : {}}/>
                    </div>
                    <div className={style.actions}>
                        <button
                            className={classNames(button.button, button.default)}
                            onClick={e => abort()}>
                            ANNULER
                        </button>
                        <button
                            onClick={e => this.checkAndConfirm()}
                            className={classNames(button.button, button.default)}>
                            AJOUTER
                        </button>
                    </div>
                </div>
            </Modal>
        )
    }
}


