import { connect } from 'react-redux'
import { importerClick } from '../actions'
import TaskList from '../components/elements/TaskList'

const mapStateToProps = state => {
    return {
        currentPath: state.importer.currentPath,
        documentsTree: state.importer.documentsTree,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onImporterClick: path => {
            dispatch(importerClick(path))
        }
    }
}

const ConnectedTaskList = connect(
    null,
    mapDispatchToProps
)(TaskList)

export default ConnectedTaskList


