import { DOCTYPE_META } from '../constants'

export const getMeta = async() => {
    try {
        const indexRef = await cozy.client.data.defineIndex(DOCTYPE_META, ['name'])
        const result = await cozy.client.data.query(indexRef, {
            selector: {
                "name": "onboarding"
            }
        })
        return result
    } catch (error) {
        return []   
    }
}

export const isAlreadyShown = async() => {

    const res = await getMeta()

    if(res.length === 0){
        return false
    } else if(res.length === 1) {
        return res[0].value
    } else {
        const value = res.shift()
        //remove all others info
        for(const e of res){
            await cozy.client.data.delete(DOCTYPE_META, e)
        }
        return value
    }
}

export const markAsShown = async(shown = true) => {
    const res = await getMeta()
    for(const e of res){
        await cozy.client.data.delete(DOCTYPE_META, e)
    }
    await cozy.client.data.create(DOCTYPE_META, {name: "onboarding", value: shown})
}


