import {
    ALERT_CLEAR,
    ALERT_DISMISS,
    ALERT_SHOW
} from '../actions'

import {ALERT_LEVEL_ERROR, ALERT_LEVEL_INFO} from '../constants'

export const alerts = (state = [], action) => {
    if (action.alert) {
        if (!action.alert.id) {
            action.alert.id = Date.now()
        }
        if(!action.alert.level) {
            action.alert.level = ALERT_LEVEL_INFO
        }
        return [action.alert, ...state]
    }
    switch (action.type) {
        case ALERT_SHOW:
            return [action.alert, ...state.filter(a => a.id !== action.id)]
        case ALERT_DISMISS:
            return state.filter(a => a.id !== action.id)
        case ALERT_CLEAR:
            return []
        default:
            return state
    }
}

export default alerts


