import React, { Component } from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import style from '../../styles/Onboarding.sass'
import Loader from './Loader'

import { markAsShown, isAlreadyShown } from '../../scripts/onboarding'

export default class Onboarding extends Component {

    constructor(props, context) {
        super(props, context)

        this.onboardingStep = [
            {
                title: "HOMEBOOK",
                content: "Bienvenue dans votre carnet numérique du logement"
            },
            {
                title: "Documents",
                content: "Disposez où que vous soyez de tous vos documents"
            },
            {
                title: "Données",
                content: "Votre logement et ses équipements, dans les moindres détails !"
            },
            {
                title: "Rappels",
                content: "Planifiez vos tâches et entretiens périodiques",
            },
            {
                title: "Contacts",
                content: "Accédez aux coordonnées de professionnels pour votre logement"
            }

        ]
        this.state = {
            activeText: this.onboardingStep[0].content,
            activeTitle: this.onboardingStep[0].title,
            currentIndex: 0,
            loading: true
        }
    }

    componentWillMount(){

        isAlreadyShown()
        .then((res) => {
            if (res) {
                return this.closeOnboarding()
            }

            return markAsShown(true)
            .then(() => {
                this.goToStep(0)
            })
        })
    }

    goToStep(key) {
       if (key > 4) {
            return this.closeOnboarding()
       }
        this.setState({
            activeText: this.onboardingStep[key].content,
            activeTitle: this.onboardingStep[key].title,
            currentIndex: key,
            loading: false
        })
    }

    closeOnboarding() {
        this.props.history.push('/accueil')
    }

    render() {
        return (
            <div className={style.container}>
                <Loader display={this.state.loading}/>
                {!this.state.loading && (
                    <div className={style.background}>
                        <div className={style.panel}>
                            <i className={classNames("ion-close",  style.close)} onClick={() => this.closeOnboarding()}/>
                            <img src="/images/logo.png" alt="Logo" className={style.logo}/>
                            <div className={style.sliderTitle}>{this.state.activeTitle}</div>
                            <div className={style.sliderText}>{this.state.activeText}</div>
                            <button className={style.button} onClick={() => this.goToStep(this.state.currentIndex+1)}>Continuer</button>
                            <ol>
                                {this.onboardingStep.map((msg, key) =>
                                    <li key={key} className={classNames(style.dot, {[style.active]: key===this.state.currentIndex})} onClick={() => this.goToStep(key)}></li>
                                )}
                            </ol>
                        </div>
                    </div>
                )}
            </div>
        )
    }
}



