import { ALERT_LEVEL_INFO } from '../constants'

export const ALERT_SHOW = 'ALERT_SHOW'
export const ALERT_DISMISS = 'ALERT_DISMISS'
export const ALERT_CLEAR = 'ALERT_CLEAR'

export const alertShow = (message, level = ALERT_LEVEL_INFO) => ({
    type: ALERT_SHOW,
    alert: {
        id : Date.now(),
        message,
        level
    }
})

export const alertDismiss = id => ({type: ALERT_DISMISS, id})

export const alertClear = () => ({type: ALERT_CLEAR})


