import React, {PureComponent} from 'react'
import style from '../../styles/Main.sass'

import bg_NRJ from '../../../public/images/Etiquette_NRJ.jpg'
import bg_CO2 from '../../../public/images/Etiquette_CO2.jpg'
import img_cursor from '../../../public/images/Etiquette_cursor.png'

export const NRJ = "NRJ"
export const CO2 = "CO2" 

const NRJValue = [
    {class: 'A', max: 50},
    {class: 'B', max: 90},
    {class: 'C', max: 150},
    {class: 'D', max: 230},
    {class: 'E', max: 330},
    {class: 'F', max: 450},
    {class: 'G', max: Infinity}
]

const CO2Value = [
    {class: 'A', max: 5},
    {class: 'B', max: 10},
    {class: 'C', max: 20},
    {class: 'D', max: 35},
    {class: 'E', max: 55},
    {class: 'F', max: 80},
    {class: 'G', max: Infinity}
]

const bgYMin = 0.125
const bgYMax = 0.865

export default class EtiquetteEnergy extends PureComponent {

    constructor(props) {
        super(props)
    }

    render() {
        return (
            <canvas ref="canvas" width={10} height={10}></canvas>
        )
    }

    componentDidMount() {
        this.createEtiquette(this.refs.canvas, this.props.value, this.props.type, this.props.size)
    }
    componentDidUpdate() {
        this.createEtiquette(this.refs.canvas, this.props.value, this.props.type, this.props.size)
    }

    createEtiquette(canvas, value, type=NRJ, size=300) {
        if(!canvas) return
            
        const parent = canvas.parentElement
        const ctx = canvas.getContext('2d')
        const w = canvas.width = Math.min(parent.clientWidth, size)
        const h = canvas.height = size
        
        const bg = new Image()
        
        let values
        
        if(type === NRJ){
            bg.src = bg_NRJ
            values = NRJValue
        } else if(type === CO2) {
            bg.src = bg_CO2
            values = CO2Value
        }

        let currentClass
        if(value === '' || typeof value !== 'string') {
            value = null
        } else {
            value = this.safeValue(value)
            value = Math.min(value, 999)
            value = Math.max(0, value) //values => [0, 999]

            //Detect class
            for(let i = 0; i < values.length; i++) {
                if(values[i].max >= value) {
                    currentClass = i
                    break
                }
            }
        }

        const bgClassHeight = (bgYMax - bgYMin) / (values.length - 1)
        const cursorPos = bgYMin + bgClassHeight * currentClass

        bg.onload = () => {
            const bgRatio = w / bg.width

            ctx.drawImage(bg, 0, 0, w, h)
            ctx.textBaseline="middle"

            if(value !== null) {
                const cursor = new Image()
                cursor.src = img_cursor
                cursor.onload = () => {
                    const cursorH = cursor.height * bgRatio
                    const cursorW = cursor.width * bgRatio
                    const textBoxRight = cursorW * 0.02 | 0

                    const right = (w - w*0.02)
                    const cursorMiddle = cursorPos * h - (cursorH/2)
                    ctx.drawImage(cursor, right - cursorW, cursorMiddle, cursorW, cursorH)        
                    
                    ctx.font = "14px Arial"
                    ctx.textAlign="right"
                    ctx.fillStyle = "white"
                    ctx.fillText(value.toFixed(1), right - textBoxRight , cursorMiddle + (cursorH/2))
                }
            } else {
                ctx.globalAlpha = 0.7
                ctx.fillStyle = "black"
                ctx.fillRect(0, 0, w, h)
                ctx.globalAlpha = 1.0
                ctx.fillStyle = "white"
                ctx.textAlign="center"
                ctx.font = "17px Arial"
                ctx.fillText("Non renseigné", w/2 , h/5)
            }
        }
    }

    safeValue(val) {
        if(val.indexOf(',') !== -1) {
            const splited = val.split(',')
            const int = +`${splited[0]}.${splited.slice(1).join('')}`
            return int
        }
        return +val
    }
}




