import * as TempFile from './TempFile'
import { parseString } from 'xml2js'
import moment from 'moment'
import _ from 'lodash'

import { ROOT_PATH, DOCTYPE_META, DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_CONTACTS } from '../constants'
import Documents from '../constants/documents.json'

import { getFileTypes } from './fileTypes'
const fileTypes = getFileTypes()

const STATUS_DONE = 'done'
const STATUS_RUN = 'running'
const STATUS_ERROR = 'errored'
const STATUS_QUEUE = 'queued'

export const autoImport = async(alert) => {

    //0. Compute zip file name
    const domain = document.getElementById('root').dataset.cozyDomain
    const slug = domain.substring(0, domain.indexOf('.'))
    const name = `homebook.${slug}.zip`

    //1. Check if there is a zip file to import
    let exist
    try {
        exist = await cozy.client.files.statByPath(ROOT_PATH + '/' + name)
    } catch (e) {
        return 0
    }

    alert('Import automatique de données en cours')

    //2. Create the temporary folder
    const tempDir = await TempFile.getTemp()

    //3. Create directory in temp to receive unziped files
    const dir = await TempFile.createFolder("unzip")

    //4. Create the jobs to unzip the file in the temp folder
    const job = await cozy.client.jobs.create('unzip', {
        "zip": exist._id,
        "destination": dir._id
    })

    //5. Wait for the end of the jobs
    try {
        await waitForJob(job._id)
    } catch (error) {
        console.error('Error in wait for job', error)
    }

    //6. Get file content
    const newDir = await cozy.client.files.statById(dir._id)
    const dirContent = newDir.relations('contents')

    if(dirContent.lenght < 1) throw Error('No file unziped')

    const id = dirContent.shift()._id
    const unzipedDir = await cozy.client.files.statById(id)
    const filesContent = unzipedDir.relations('contents').map(f => ({id: f._id, name: removeDiacritics(f.attributes.name).toLowerCase()}))

    //7. Tag files and move it to the right place
    for(const file of filesContent) {
        if(file.name.indexOf('xml_rset') === 0) {
            try {
                //extract data from xml
                const link = await cozy.client.files.getDownloadLinkById(file.id)
                const fullLink = await cozy.client.fullpath(link)
                const res = await fetch(fullLink)
                const data = await res.text()
                const xml = await readXml(data)

                //create cozy data
                //contacts
                for(let con of xml.contacts) {
                    await cozy.client.data.create(DOCTYPE_CONTACTS, con)
                }
                //homeData
                await cozy.client.data.create(DOCTYPE_HOMEDATA, {...xml.homeData, status:"Propriétaire", type:"Logement entier"})
                //equipments
                for(let eq of xml.equipments) {
                    await cozy.client.data.create(DOCTYPE_EQUIPMENTS, eq)
                }
                //diagnoses
                for(let diag of xml.diagnoses) {
                    await cozy.client.data.create(DOCTYPE_DIAGNOSIS, diag)
                }

            } catch (error) {
                console.error('Error in reading XML', error)
            }
        }

        const tag = getTagOf(file.name)
        if(tag !== null) {
            const path = getPathOf(tag)
            if(path === null) throw Error('No path for ' + tag)
            
            const parent = await cozy.client.files.statByPath(path)
            try {
                await cozy.client.files.updateAttributesById(file.id, {dir_id: parent._id, tags:[tag]})
                console.log('Move file', file, path, tag);
            } catch (error) {}
        } else {

            if(file.name === 'tradimaison') {
                console.log('Detect tradimaison');
                await saveActor('tradimaisons')
            } else {
                console.log('Unknown file', file)
            }

        }
    }

    //8. Remove the zip file
    await cozy.client.files.trashById(exist._id)
    await cozy.client.files.destroyById(exist._id)

    return 1 //status OK
}


const getTagOf = name => {
    for(const d of Documents) {
        if(name.indexOf(d.name) !== -1) return d.tag
    }
    return null
}

const getPathOf = tag => {
    for (const t of fileTypes) {
        if (t.key === tag) 
            return t.location
    }
    return null
}

export const waitForJob = async(id) => {

    const job = await cozy.client.fetchJSON('GET', '/jobs/' + id)
    const status = job.attributes.state

    if(status === STATUS_DONE) return
    if(status === STATUS_ERROR) throw Error('Job fail', job.attributes.error)

    return new Promise((resolve, reject) => {
        setTimeout(() => {
            waitForJob(id)
            .then(() => resolve())
        }, 10)
    })
}

const saveActor = async(name) => {
    let meta = []
    try {
        const indexRef = await cozy.client.data.defineIndex(DOCTYPE_META, ['name'])
        meta = await cozy.client.data.query(indexRef, {
            selector: {
                "name": "actors"
            }
        })
    } catch (error) {}
    for(const e of meta){
        await cozy.client.data.delete(DOCTYPE_META, e)
    }
    await cozy.client.data.create(DOCTYPE_META, {name: "actors", actor: name})
}

const readXml = async(data) => {

    const extractAddress = adresse => ({way: adresse.ligne, code: adresse.code_postal, city: adresse.ville})

    return new Promise((resolve, reject) => {
        parseString(data, {strict: true, async: true, explicitArray: false}, (err, result) => {
            if(err !== null) return reject(err)

            let ret = {
                contacts: [],
                homeData: {
                    name: 'Données importées',
                    constructionDate: null,
                    address: null,
                    livingArea: null
                },
                equipments: [],
                diagnoses: []
            }

            //1. extract contacts
            const contacts = _.property('projet.Datas_Comp.donnees_generales')(result)

            if(contacts) {
                if(contacts.BET) {
                    ret.contacts.push({
                        company: contacts.BET.nom,
                        telephones: [{
                            type: "FIXE",
                            phone: contacts.BET.tel,
                        }],
                        address : extractAddress(contacts.BET.adresse)
                    })
                }
                if(contacts.bureau_controle) {
                    ret.contacts.push({
                        company: contacts.bureau_controle.nom,
                        telephones: [{
                            type: "FIXE",
                            phone: contacts.bureau_controle.tel,
                        }],
                        address: extractAddress(contacts.bureau_controle.adresse)
                    })
                }
                if(contacts.maitre_oeuvre) {
                    ret.contacts.push({
                        company: contacts.maitre_oeuvre.nom,
                        telephones: [{
                            type: "FIXE",
                            phone: contacts.maitre_oeuvre.tel,
                        }],
                        address: extractAddress(contacts.maitre_oeuvre.adresse)
                    })
                }
                if(contacts.maitre_ouvrage) {
                    ret.contacts.push({
                        lastName: contacts.maitre_ouvrage.nom,
                        telephones: [{
                            type: "FIXE",
                            phone: contacts.maitre_ouvrage.tel
                        }],
                        address: extractAddress(contacts.maitre_ouvrage.adresse)
                    })
                }
            }


            //2. extract home data
            let date = _.property('projet.Entree_Projet.Date')(result)
            if(date) {
                ret.homeData.constructionDate = moment(date, 'YYYYMMDD').format('DD/MM/YYYY')
            }

            ret.homeData.address = extractAddress(_.property('projet.Datas_Comp.donnees_generales.operation.adresse')(result))
            ret.homeData.livingArea = _.property('projet.Datas_Comp.batiment_collection.batiment.zone_collection.zone.O_SHAB')(result)

            //3. extract equipments
            const zone = _.property('projet.Entree_Projet.Batiment_Collection.Batiment.Zone_Collection.Zone')(result)

            //chaudière
            let name =  _.property('projet.Entree_Projet.Generation_Collection.Generation.Generateur_Collection.Generateur_Combustion.Name')(result)
            if(name) {
                ret.equipments.push({
                    type: 'Chaudière à gaz',
                    name
                })
            }

            //Photovoltaïque
            name = _.property('projet.Entree_Projet.Batiment_Collection.Batiment.PV_install_Collection.PV_install.Name')(result)
            if(name) {
                ret.equipments.push({
                    type: 'Installation photovoltaïques',
                    name
                })
            }

            //Radiateurs électriques
            name = _.property('Emetteur_Collection.Emetteur.Name')(zone)
            if(name) {
                ret.equipments.push({
                    type: 'Radiateurs électriques',
                    name
                })
            }

            //Eau chaude sanitaire électrique
            name = _.property('Emetteur_ECS_Collection.Emetteur_ECS.Name')(zone)
            if(name) {
                ret.equipments.push({
                    type: 'Eau chaude sanitaire électrique',
                    name
                })
            }

            //Ventilation naturelle
            name = _.property('Entree_Air_Collection.Entree_Air.Name')(zone)
            if(name) {
                ret.equipments.push({
                    type: 'Ventilation naturelle',
                    name
                })
            }

            //Eclairage
            name = _.property('Eclairage_Collection.Eclairage.Name')(zone)
            if(name) {
                ret.equipments.push({
                    name
                })
            }

            //VMC
            name = _.property('projet.Entree_Projet.Batiment_Collection.Batiment.Zone_Collection.Zone.Ventilation_Mecanique_Collection.Ventilation_Mecanique.Name')(result)
            if(name) {
                ret.equipments.push({
                    type: 'VMC',
                    name
                })
            }

            //4. extract Diagnoses
            let diag = {
                bbio : null,
                cep : null,
                tic : null,
                date : null,
                CO2emission : "",
                DPEEnergyClass : "",
                carbonClass : "",
                data : {},
                equipmentData : {},
                expirationDate : "",
                name : "DPE initiale",
                realEnergyConsommation : "",
                type : "DPE",
            }

            diag.bbio = _.property('projet.Sortie_Projet.Sortie_Batiment_B_Collection.Sortie_Batiment_B.O_Bbio_pts_annuel')(result)
            diag.cep = _.property('projet.Sortie_Projet.Sortie_Batiment_C_Collection.Sortie_Batiment_C.O_Cep_annuel')(result)
            diag.tic = _.property('projet.Sortie_Projet.Sortie_Batiment_E_Collection.Sortie_Batiment_E.Sortie_Zone_E_Collection.Sortie_Zone_E.Sortie_Groupe_E_Collection.Sortie_Groupe_E.O_Tic')(result)
            date = _.property('projet.Datas_Comp.donnees_generales.BET.date_etude')(result)
            if(date) {
                diag.date = moment(date, 'YYYY-MM-DD').format('DD/MM/YYYY')
            }
            ret.diagnoses.push(diag)

            resolve(ret)
        })
    })

}

