import React, {Component} from 'react'
import classNames from 'classnames'
import { STATUS_DONE, STATUS_LOAD } from '../../constants'
import button from '../../styles/buttons.scss'
import ToolBoxOptions from './ToolBoxOptions'
import _ from 'lodash'

import Page from './Page'
import Loader from './Loader'
import DocumentList from './DocumentList'
import ContactList from './ContactList'
import DataList from './DataList'

import { VIEW_GRID, VIEW_LIST } from '../../constants'

export default class Trash extends Component{

    constructor(props, context) {
        super(props, context)

        this.state = {
            current: null,
            modalOpen: false
        }
    }

    componentWillMount () {
        this.props.onLoadTrash()
    }

    componentWillReceiveProps (newProps) {
        if(newProps.contacts !== undefined && newProps.files !== undefined && newProps.data !== undefined && newProps.count !== undefined) {
            if((newProps.contacts.length + newProps.files.length + newProps.data.length )!== newProps.count) newProps.onLoadTrash()
        } else {
            newProps.onLoadTrash()
        }
    }

    empty() {
        this.props.onEmpty()
    }

    restore() {
        const selectedFiles = this.props.files.filter(f => f.selected)
        if(selectedFiles.length > 0)
            this.props.onRestoreDocuments(selectedFiles)

        const selectedContacts = this.props.contacts.filter(f => f.selected)
        if(selectedContacts.length > 0)
            this.props.onRestoreContacts(selectedContacts)

        const selectedData = this.props.data.filter(f => f.selected)
        if(selectedData.length > 0)
            this.props.onRestoreData(selectedData)
    }

    render() {
        const { files, contacts, data, count, selectedCount, status } = this.props

        const loading = status === STATUS_LOAD

        const empty = (<button onClick={() => this.empty()} className={classNames(button.button, button.assertive)}><i className="ion-ios-trash-outline"></i>VIDER</button>)
        const toolbox = (
            <ToolBoxOptions>
                <ul>
                    <li onClick={() => {
                        this.props.onSelectAll(this.props.count)
                            
                    }}>
                        <i className="ion-ios-checkmark-outline">TOUT SELECTIONNER</i>
                    </li>
                    <li onClick={() => {
                        this.props.onChangeViewMode()
                    }}>
                        {this.props.viewMode === VIEW_LIST ? (
                            <i className="ion-grid">VUE GRILLE</i>
                        ) : (
                            <i className="ion-navicon">VUE LISTE</i>
                        )}
                    </li>

                </ul>
            </ToolBoxOptions>
        )
        const restore = (<button onClick={() => this.restore()} className={classNames(button.button, button.stable)}><i className="ion-ios-undo-outline"></i>RESTAURER</button>)

        const actions = [toolbox]

        if(count > 0) actions.unshift(empty)
        if(selectedCount > 0) actions.unshift(restore)

        return (
            <Page title="Corbeille" subtitle={count !== null && (count + (count > 1 ? ' éléments' : ' élément'))} 
            actions={actions}>
                <Loader display={loading}/> 

                <div className="files">
                    {!loading && files.length > 0 && (
                        <DocumentList view={this.props.viewMode} onClickItem={item => {
                            this.props.onSelectFile(item)  
                        }}>
                            {files}
                        </DocumentList>
                    )}

                    {!loading && files.length === 0 && "Aucun fichier"}
                </div>

                <div className="contacts">
                    {!loading && contacts.length > 0 && (
                        <ContactList view={this.props.viewMode} onClickItem={item => {
                            this.props.onSelectContact(item)  
                        }}>
                            {contacts}
                        </ContactList>
                    )}

                    {!loading && contacts.length === 0 && "Aucun contact"}
                </div>

                <div className="data">
                    {!loading && data.length > 0 && (
                        <DataList view={this.props.viewMode} onClickItem={item => {
                            this.props.onSelectData(item)  
                        }}>
                            {data}
                        </DataList>
                    )}

                    {!loading && data.length === 0 && "Aucune donnée"}
                </div>

            </Page>
        )
    }
}


