import { DOCTYPE_CONTACTS } from '../constants'

export const CONTACTS_NEW = "CONTACTS_NEW"
export const CONTACTS_NEW_SUCCESS = "CONTACTS_NEW_SUCCESS"
export const CONTACTS_NEW_FAILURE = "CONTACTS_NEW_FAILURE"
export const CONTACTS_SAVE = "CONTACTS_SAVE"
export const CONTACTS_SAVE_SUCCESS = "CONTACTS_SAVE_SUCCESS"
export const CONTACTS_SAVE_FAILURE = "CONTACTS_SAVE_FAILURE"
export const CONTACTS_REMOVE = "CONTACTS_REMOVE"
export const CONTACTS_REMOVE_SUCCESS = "CONTACTS_REMOVE_SUCCESS"
export const CONTACTS_REMOVE_FAILURE = "CONTACTS_REMOVE_FAILURE"
export const CONTACTS_RESTORE = "CONTACTS_RESTORE"
export const CONTACTS_RESTORE_SUCCESS = "CONTACTS_RESTORE_SUCCESS"
export const CONTACTS_RESTORE_FAILURE = "CONTACTS_RESTORE_FAILURE"
export const CONTACTS_SELECT = "CONTACTS_SELECT"

export const contactsSelect = contact => ({type: CONTACTS_SELECT, contact}) //or with id

export const contactsRemove = contacts => {
    return async(dispatch, getState) => {
        dispatch({type: CONTACTS_REMOVE, contacts})
        try {
            const removed = []
            for(const contact of contacts) {
                removed.push(await cozy.client.data.updateAttributes(DOCTYPE_CONTACTS, contact._id, {trash: true}))
            }
            return dispatch({type: CONTACTS_REMOVE_SUCCESS, ids: removed.map(f => f._id)})
        } catch (err) {
            return dispatch({type: CONTACTS_REMOVE_FAILURE, err})            
        }

    }
}

export const contactsRestore = contacts => {
    return async(dispatch, getState) => {
        dispatch({type: CONTACTS_RESTORE, contacts})
        try {
            const restored = []
            for(const contact of contacts) {
                restored.push(await cozy.client.data.updateAttributes(DOCTYPE_CONTACTS, contact._id, {trash: false}))
            }
            return dispatch({type: CONTACTS_RESTORE_SUCCESS, ids: restored.map(f => f._id)})
        } catch (err) {
            return dispatch({type: CONTACTS_RESTORE_FAILURE, err})            
        }

    }
}

export const contactsNew = (name, data) => {
    return async(dispatch, getState) => {
        dispatch({type: CONTACTS_NEW, name})

        try {
            const contact = await cozy.client.data.create(DOCTYPE_CONTACTS, data)
            return dispatch({type: CONTACTS_NEW_SUCCESS, contact})
        } catch(err) {
            return dispatch({type: CONTACTS_NEW_FAILURE, err})
        }

    }
}

export const contactsSave = (id,data) => {
    return async(dispatch, getState) => {
        dispatch({type: CONTACTS_SAVE, id})

        try {
            const contact = await cozy.client.data.updateAttributes(DOCTYPE_CONTACTS, id, data)
            return dispatch({type: CONTACTS_SAVE_SUCCESS, contact})
        } catch(err) {
            return dispatch({type: CONTACTS_SAVE_FAILURE, err})
        }

    }
}


