import { connect } from 'react-redux'
import { contactsNew, contactsRemove, contactsSave, contactsSelect, loadContacts } from '../actions'
import Contacts from '../components/elements/Contacts'

import confirm from '../scripts/confirm'

import React from 'react'
import ConfirmModal from '../components/modals/ConfirmModal'

const modal = (
    <ConfirmModal>
        <p>
            Voulez-vous supprimer ce contact ?
        </p>
    </ConfirmModal>
)

const mapStateToProps = state => {
    return {
        elements: state.contacts.elements,
        count: state.contacts.elementCount,
        selectedCount: state.contacts.selectedCount,
        status: state.contacts.fetchStatus
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onSelectContact: contact => {
            dispatch(contactsSelect(contact))
        },
        onNewContact: (name, data) => {
            dispatch(contactsNew(name, data))
        },
        onRemoveContacts: contacts => {
            confirm(modal)
            .then(() => dispatch(contactsRemove(contacts)))
            .catch(err => {})
        },
        onSaveContact: (id, data) => {
            dispatch(contactsSave(id, data))
        },
        onLoadContacts: () => {
            dispatch(loadContacts())
        }
    }
}

const ConnectedContacts = connect(
    mapStateToProps,
    mapDispatchToProps
)(Contacts)

export default ConnectedContacts


