import { connect } from 'react-redux'
import { importerClick, importerDirectoryChange, importerFilesUpload, importerTree, importerViewChange } from '../actions'
import Importer from '../components/modals/ImporterModal'

const mapStateToProps = state => {
    return {
        currentType: state.importer.currentType,
        currentPath: state.importer.currentPath,
        documentsTree: state.importer.documentsTree,
        view: state.importer.view,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onDirectoryChange: path => {
            dispatch(importerDirectoryChange(path))
        },
        onImport: (docs, dirId) => {
            dispatch(importerFilesUpload(docs, dirId))
        },
        onTree: () => {
            dispatch(importerTree())
        },
        onChangeView: (view) => {
            dispatch(importerViewChange(view))
        }
    }
}

const ConnectedImporter = connect(
    mapStateToProps,
    mapDispatchToProps
)(Importer)

export default ConnectedImporter


