import React, {Component} from 'react'
import classNames from 'classnames'
import button from '../../styles/buttons.scss'

import { getTypesByGroup } from '../../scripts/fileTypes'
const typesByGroup = getTypesByGroup()

export default class TypeChanger extends Component {

    constructor(props, context) {
        super(props, context)

        this.closeIntent = null
        this.type = this.props.type

        this.state = {
            newType: this.props.type
        }
    }

    cancelChange() {
        this.closeIntent = setTimeout(() => {
            this.props.onCancel()
        }, 100)
    }

    stopCancelChange() {
        clearTimeout(this.closeIntent)
    }

    checkNewType(newType) {
        this.setState({newType})
    }

    change() {
        if (this.name === this.state.newType) 
            return this.props.onCancel()

        this.props.onChange(this.state.newType)
    }

    render() {
        return (
            <span style={{display: 'inline-flex'}}>
                <select
                    autoFocus
                    value={this.state.newType}
                    onChange={e => this.checkNewType(e.target.value)} 
                    onKeyPress={(e) => {
                        if (e.key === 'Enter')
                            this.change()
                    }}>
                    <option value="">Choisir un type</option>
                    {typesByGroup.map((item, key1) => (
                        <optgroup label={item.name} key={key1}>
                            {item.types.map((type, key2) => (
                                <option key={key2} value={type.key}>{type.name}</option>
                            ))}
                        </optgroup>
                    ))}
                    <option value="">Autre</option>
                </select>
                <button
                    className={classNames(button.button, button.stable)}
                    onFocus={(e) => this.stopCancelChange()}
                    onClick={() => this.change()}>
                    <i className="ion-ios-checkmark-empty"></i>
                </button>
                <button
                    className={classNames(button.button, button.assertive)}
                    onClick={(e) => this.cancelChange()}>
                    <i className="ion-ios-close-empty"></i>
                </button>
            </span>
        )
    }
}


