import {
    combineReducers
} from 'redux'

import {
    LOAD_DOCS,
    LOAD_DOCS_SUCCESS,
    LOAD_DOCS_FAILURE,
    LOAD_TRASH_SUCCESS,
    DOCUMENTS_NEW_FOLDER,
    DOCUMENTS_NEW_FOLDER_SUCCESS,
    DOCUMENTS_REMOVE,
    DOCUMENTS_REMOVE_SUCCESS,
    DOCUMENTS_REMOVE_FAILURE,
    IMPORTER_FILE_UPLOAD,
    IMPORTER_FILE_UPLOAD_SUCESS,
    DOCUMENTS_RESTORE,
    DOCUMENTS_RESTORE_SUCCESS,
    DOCUMENTS_RESTORE_FAILURE,
    DOCUMENTS_SELECT,
    DOCUMENTS_INITIALIZE_SUCCESS,
    DOCUMENTS_CHANGE_FILTER,
    LOAD_FILE_SUCCESS,
    DOCUMENTS_RENAME_SUCCESS,
    DOCUMENTS_MOVE_SUCCESS,
    DOCUMENTS_CHANGE_TYPE_SUCCESS
} from '../actions'

import { STATUS_LOAD, STATUS_DONE, STATUS_ERROR, SORT_DATE, SORT_NAME, TYPE_FILE, TYPE_FOLDER } from '../constants'

// reducer for the currently displayed folder properties
const folder = (state = null, action) => {
    switch (action.type) {
        case LOAD_DOCS_SUCCESS:
            return action.folder
        default:
            return state
    }
}

const filter = (state = SORT_DATE, action) => {
    switch (action.type) {
        case DOCUMENTS_CHANGE_FILTER:
            return action.filter
        default:
            return state
    }
}


const path = (state = null, action) => {
    switch (action.type) {
        case LOAD_DOCS:
            return action.path
        default:
            return state
    }
}

const fileCount = (state = 0, action) => {
    switch (action.type) {
        case LOAD_DOCS_SUCCESS:
            return action.fileCount
        case IMPORTER_FILE_UPLOAD_SUCESS:
            return state + action.files.length
        case DOCUMENTS_NEW_FOLDER_SUCCESS:
            return state + 1
        case DOCUMENTS_REMOVE_SUCCESS:
        case DOCUMENTS_MOVE_SUCCESS:
            return state - action.ids.length
        case DOCUMENTS_RESTORE_SUCCESS:
            return state + action.ids.length
        default:
            return state
    }
}

const selectedCount = (state = 0, action) => {
    switch (action.type) {
        case LOAD_DOCS_SUCCESS:
        case DOCUMENTS_REMOVE_SUCCESS:
        case DOCUMENTS_RENAME_SUCCESS:
        case DOCUMENTS_MOVE_SUCCESS:
            return 0
        case DOCUMENTS_SELECT:
            if(action.document.selected)
                return state - 1
            else
                return state + 1
        default:
            return state
    }
}

// reducer for the full file list of the currently displayed folder
const files = (state = [], action) => {
    switch (action.type) {
        case LOAD_DOCS_SUCCESS:
            return action.files
            
        case IMPORTER_FILE_UPLOAD_SUCESS:
            if(action.currentDir)
                return [...state, ...action.files]
            else
                return state
        case DOCUMENTS_NEW_FOLDER_SUCCESS:
            return [action.folder, ...state]

        case DOCUMENTS_REMOVE_SUCCESS:
        case DOCUMENTS_MOVE_SUCCESS:
            return state.filter(f => action.ids.indexOf(f.id) === -1)

        case DOCUMENTS_SELECT:
            const updated = state.map(f => {
                if(f.id === action.document.id) f.selected = !f.selected
                return f
            })
            return [...updated]

        case DOCUMENTS_RENAME_SUCCESS:
            const updatedName = state.map(f => {
                if(f.id === action.file.id) f = action.file
                return f
            })
            return [...updatedName]
        default:
            return state
    }
}

const fetchStatus = (state = STATUS_LOAD, action) => {
    switch (action.type) {
        // there's a trick here : we set the fetchStatus to 'pending'
        // on initial state so that the loading spinner is only showed
        // when the app is launched or when the user use the back button
        case LOAD_DOCS: // we temporarily display the spinner when working in the trashed
        case DOCUMENTS_REMOVE: // TODO: display a spinner in the confirm modal instead
        case DOCUMENTS_NEW_FOLDER:
        case IMPORTER_FILE_UPLOAD:
            return STATUS_LOAD
        case LOAD_DOCS_SUCCESS:
        case LOAD_FILE_SUCCESS:
        case DOCUMENTS_REMOVE_SUCCESS:
        case IMPORTER_FILE_UPLOAD_SUCESS:
        case DOCUMENTS_MOVE_SUCCESS:
        case DOCUMENTS_NEW_FOLDER_SUCCESS:
            return STATUS_DONE
        case DOCUMENTS_REMOVE_FAILURE:
        case LOAD_DOCS_FAILURE:
            return STATUS_ERROR
        default:
            return state
    }
}

const lastFetchType = (state = null, action) => {
    switch (action.type) {
        case LOAD_DOCS:
        case LOAD_DOCS_SUCCESS:
            return TYPE_FOLDER
        case LOAD_FILE_SUCCESS:
            return TYPE_FILE
        default:
            return state
    }
}

const needSort = (state = true, action) => {
    switch (action.type) {
        case DOCUMENTS_CHANGE_FILTER:
        case IMPORTER_FILE_UPLOAD_SUCESS:
        case LOAD_DOCS_SUCCESS:
            return true
        default:
            return false
    }
}

const current = (state=null, action) => {
    switch (action.type) {
        case LOAD_FILE_SUCCESS:
        case DOCUMENTS_CHANGE_TYPE_SUCCESS:
            return action.file
        case LOAD_DOCS_SUCCESS:
            return null
        default:
            return state
    }
}

export default combineReducers({
    folder,
    path,
    fileCount,
    selectedCount,
    files,
    fetchStatus,
    lastFetchType,
    filter,
    needSort,
    current
})


