import { connect } from 'react-redux'
import { recallsCalendarClick, recallsNew, recallsRemove, recallsSave, loadRecalls } from '../actions'
import Recalls from '../components/elements/Recalls'

import confirm from '../scripts/confirm'

import React from 'react'
import ConfirmModal from '../components/modals/ConfirmModal'

const modal = (
    <ConfirmModal>
        <p>
            Voulez-vous supprimer ce rappel ?
        </p>
    </ConfirmModal>
)

const mapStateToProps = state => {
    return {
        elements: state.recalls.elements,
        count: state.recalls.elementCount,
        status: state.recalls.fetchStatus
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onNewRecall: (name, data) => {
            dispatch(recallsNew(name, data))
        },
        onRemoveRecalls: recalls => {
            confirm(modal)
            .then(() => dispatch(recallsRemove(recalls)))
            .catch(err => {})
        },
        onSaveRecall: (id, data) => {
            dispatch(recallsSave(id, data))
        },
        onClick: (id) => {
            dispatch(recallsCalendarClick(id))
        },
        onLoadRecalls: () => {
            dispatch(loadRecalls())
        }
    }
}

const ConnectedRecalls = connect(
    mapStateToProps,
    mapDispatchToProps
)(Recalls)

export default ConnectedRecalls


