import React, {Component} from 'react'
import classNames from 'classnames'
import Portal from 'react-portal'
import BigCalendar from 'react-big-calendar'
import _ from 'lodash'
import moment from 'moment'
import 'moment/locale/fr'
import 'react-big-calendar/lib/css/react-big-calendar.css'
import message from 'react-big-calendar/lib/utils/messages'
import {navigate} from 'react-big-calendar/lib/utils/constants'


import { STATUS_LOAD } from '../../constants'
import button from '../../styles/buttons.scss'
import modal from '../../styles/Modal.sass'
import style from '../../styles/Recalls.sass'

import Page from './Page'
import Loader from './Loader'

import Modal from '../modals/Modal'
import RecallsModal from '../modals/RecallsModal'

moment.locale('fr')
BigCalendar.momentLocalizer(moment);

export default class Recalls extends Component{

    constructor(props, context) {
        super(props, context)

        this.state = {
            current: null,
            modalOpen: false,
            defaultDate: null,
            edition: false
        }
    }

    componentWillMount () {
        this.props.onLoadRecalls()
    }

    componentWillReceiveProps (newProps) {
    }

    addOrSaveRecall(recall) {
        if(this.state.current) {
            this.props.onSaveRecall(this.state.current._id, Object.assign(this.state.current, recall))
        } else {
            this.props.onNewRecall(recall.name, recall)
        }
        this.setState({
            modalOpen: false,
            current: null
        })
    }

    add() {
        this.setState({
            current: null,
            modalOpen: true,
            defaultDate: undefined,
            edition: true
        })
    }

    open(item) {
        this.setState({
            current: item,
            modalOpen: true
        })
    }

    remove() {
        if(!this.state.current) return
        this.props.onRemoveRecalls([this.state.current])
        this.setState({
            modalOpen: false,
            current: null,
            defaultDate: undefined
        })
    }

    render() {
        const { elements, count, status } = this.props

        const loading = status === STATUS_LOAD

        const add = (<button onClick={() => this.add()} className={classNames(button.button, button.default)}><i className="ion-ios-plus-empty"></i>AJOUTER</button>)
    
        const EventAgenda = ({ event }) => {
            return (
                <span>
                    <p className="agenda-title" onClick={() => this.setState({modalOpen: true, current: event.item, defaultDate: undefined})}>{event.title}</p>
                    <p onClick={() => this.setState({modalOpen: true, current: event.item, defaultDate: undefined})}>{ event.item.deadline }</p>
                </span>
            )
        }

        return (
            <Page title="Rappels" subtitle={count !== null && (count + (count > 1 ? ' rappels' : ' rappel'))} 
            actions={[add]}>
                <Loader display={loading}/> 

                {!loading && (
                    <BigCalendar
                        events={elements.map(item => {
                            let date = moment(item.deadline, 'DD/MM/YYYY')
                            if(date.isValid()) {
                                date = date.toDate()
                            } else if(_.isDate(item.deadline)){
                                date = item.deadline
                            } else {
                                date = Date.now()
                            }

                            return {
                                title: item.name,
                                allDay: true,
                                start: date ,
                                end: date,
                                item: item
                            }
                        })}
                        selectable={true}
                        popup
                        components={{agenda: {event: EventAgenda}, toolbar: MyToolbar}}
                        views={['month', 'agenda']}
                        onSelectSlot={e => this.setState({modalOpen: true, current: null, defaultDate: e.end, edition: true})}
                        onSelectEvent={e => this.setState({modalOpen: true, current: e.item, defaultDate: undefined})}
                        messages={{
                            allDay: 'Journée',
                            previous: 'Précédent',
                            next: 'Suivant',
                            today: 'Aujourd\'hui',
                            month: 'Mois',
                            agenda: 'Agenda',
                            showMore: n => `+${n} plus`,
                            time: 'Heure',
                            event: 'Événement'
                        }}
                        style={{flex: '1 0 auto', minHeight: '500px'}}
                    />
                )}

                <Portal isOpened={this.state.modalOpen} closeOnEsc onClose={() => this.setState({modalOpen: false, current: null, defaultDate: undefined, edition: false})}>
                    <Modal>
                        <RecallsModal
                            defaultDate={this.state.defaultDate}
                            item={this.state.current}
                            title={this.state.current ? "Modifier rappel" : "Nouveau rappel"}
                            onSave={recall => this.addOrSaveRecall(recall)}
                            onRemove={() => this.remove()}
                            edit={this.state.edition}/>
                    </Modal>
                </Portal> 
            </Page>
        )
    }
}

class MyToolbar extends React.Component {

    render() {
        let {messages, label} = this.props

        messages = message(messages)

        return (
            <div className='rbc-toolbar' style={{flexWrap:'wrap'}}>
                <span className='rbc-btn-group'>
                    <button
                        type='button'
                        onClick={this
                        .navigate
                        .bind(null, navigate.TODAY)}>
                        {messages.today}
                    </button>
                    <button
                        type='button'
                        onClick={this
                        .navigate
                        .bind(null, navigate.PREVIOUS)}>
                        {messages.previous}
                    </button>
                    <button
                        type='button'
                        onClick={this
                        .navigate
                        .bind(null, navigate.NEXT)}>
                        {messages.next}
                    </button>
                </span>

                <span className='rbc-toolbar-label' style={{width: 'auto', flex: '1 0 auto'}}>
                    {label}
                </span>

                <span className='rbc-btn-group'>
                    {this.viewNamesGroup(messages)}
                </span>
            </div>
        )
    }

    navigate = (action) => {
        this
            .props
            .onNavigate(action)
    }

    view = (view) => {
        this
            .props
            .onViewChange(view)
    }

    viewNamesGroup(messages) {
        let viewNames = this.props.views
        const view = this.props.view

        if (viewNames.length > 1) {
            return (viewNames.map(name => <button
                type='button'
                key={name}
                className={classNames({
                'rbc-active': view === name
            })}
                onClick={this
                .view
                .bind(null, name)}>
                {messages[name]}
            </button>))
        }
    }
}


