import {
    isDirectory,
    extractFileAttributes,
    forceFileDownload
} from '../tools'

import { ROOT_PATH, FOLDER_LIST, COZY_ROOT_ID, DATA_LIST, ALERT_LEVEL_ERROR } from '../constants'

import { loadFolder } from './load'
import { gaugeInitialize } from './gauge'
import { importerTree } from './importer'

export const DOCUMENTS_SELECT = "DOCUMENTS_SELECT"
export const DOCUMENTS_REMOVE = "DOCUMENTS_REMOVE"
export const DOCUMENTS_REMOVE_SUCCESS = "DOCUMENTS_REMOVE_SUCCESS"
export const DOCUMENTS_REMOVE_FAILURE = "DOCUMENTS_REMOVE_FAILURE"
export const DOCUMENTS_RESTORE = "DOCUMENTS_RESTORE"
export const DOCUMENTS_RESTORE_SUCCESS = "DOCUMENTS_RESTORE_SUCCESS"
export const DOCUMENTS_RESTORE_FAILURE = "DOCUMENTS_RESTORE_FAILURE"
export const DOCUMENTS_INITIALIZE = "DOCUMENTS_INITIALIZE"
export const DOCUMENTS_INITIALIZE_SUCCESS = "DOCUMENTS_INITIALIZE_SUCCESS"
export const DOCUMENTS_INITIALIZE_FAILURE = "DOCUMENTS_INITIALIZE_FAILURE"
export const DOCUMENTS_DOWNLOAD = "DOCUMENTS_DOWNLOAD"
export const DOCUMENTS_DOWNLOAD_ALL = "DOCUMENTS_DOWNLOAD_ALL"
export const DOCUMENTS_CHANGE_VIEW_MODE = "DOCUMENTS_CHANGE_VIEW_MODE"
export const DOCUMENTS_NEW_FOLDER = "DOCUMENTS_NEW_FOLDER"
export const DOCUMENTS_NEW_FOLDER_SUCCESS = "DOCUMENTS_NEW_FOLDER_SUCCESS"
export const DOCUMENTS_NEW_FOLDER_FAILURE_GENERIC = "DOCUMENTS_NEW_FOLDER_FAILURE_GENERIC"
export const DOCUMENTS_NEW_FOLDER_FAILURE_DUPLICATE = "DOCUMENTS_NEW_FOLDER_FAILURE_DUPLICATE"
export const DOCUMENTS_CLICK = "DOCUMENTS_CLICK"
export const DOCUMENTS_CHANGE_FILTER = "DOCUMENTS_CHANGE_FILTER"
export const DOCUMENTS_RENAME = "DOCUMENTS_RENAME"
export const DOCUMENTS_RENAME_SUCCESS = "DOCUMENTS_RENAME_SUCCESS"
export const DOCUMENTS_RENAME_FAILURE = "DOCUMENTS_RENAME_FAILURE"
export const DOCUMENTS_MOVE = "DOCUMENTS_MOVE"
export const DOCUMENTS_MOVE_SUCCESS = "DOCUMENTS_MOVE_SUCCESS"
export const DOCUMENTS_MOVE_FAILURE = "DOCUMENTS_MOVE_FAILURE"
export const DOCUMENTS_CHANGE_TYPE = "DOCUMENTS_CHANGE_TYPE"
export const DOCUMENTS_CHANGE_TYPE_SUCCESS = "DOCUMENTS_CHANGE_TYPE_SUCCESS"
export const DOCUMENTS_CHANGE_TYPE_FAILURE = "DOCUMENTS_CHANGE_TYPE_FAILURE"

export const HTTP_CODE_CONFLICT = 409

export const documentsSelect = document => ({
    type: DOCUMENTS_SELECT,document
})
export const documentsChangeFilter = filter => ({
    type: DOCUMENTS_CHANGE_FILTER, filter
})
export const documentsChangeViewMode = () => ({
    type: DOCUMENTS_CHANGE_VIEW_MODE
})
export const documentsClick = document => ({
    type: DOCUMENTS_CLICK,
    document
})

export const documentsDownload = selected => {
    console.log('Download', selected);
    return async(dispatch) => {
        if (selected.length === 1 && !isDirectory(selected[0])) {
            return dispatch(downloadFile(selected[0]))
        }
        const paths = selected.map(f => f.path)
        const href = await cozy.client.files.getArchiveLinkByPaths(paths)
        const fullpath = await cozy.client.fullpath(href)
        forceFileDownload(fullpath, 'homebook.zip')
        return dispatch({
            type: DOCUMENTS_DOWNLOAD,
            selected
        })
    }
}

const downloadFile = file => {
    return async(dispatch) => {
        const response = await cozy.client.files.downloadById(file.id).catch((error) => {
            throw error
        })
        const blob = await response.blob()
        const filename = file.name

        forceFileDownload(window.URL.createObjectURL(blob), filename)
        return dispatch({
            type: DOCUMENTS_DOWNLOAD,
            file
        })
    }
}

export const documentsNewFolder = name => {
    if(!name) {
        return {
            type: DOCUMENTS_NEW_FOLDER_FAILURE_GENERIC,
            alert: {
                message: 'Veuillez donner un nom au nouveau dossier'
            }
        }
    }

    return async(dispatch, getState) => {
        const files = getState().documents.files
        const existingFolder = files && files.find(f => isDirectory(f) && f.name === name)
        if (existingFolder) {
            return dispatch({
                type: DOCUMENTS_NEW_FOLDER_FAILURE_DUPLICATE,
                alert: {
                    message: `L'élément ${name} existe déjà, merci de choisir un nouveau nom.`
                }
            })
        }

        dispatch({
            type: DOCUMENTS_NEW_FOLDER,
            name
        })

        try {
            const parent = getState().documents.folder
            const folder = await cozy.client.files.createDirectory({
                name,
                dirID: parent.id
            })
            dispatch(importerTree())
            dispatch({
                type: DOCUMENTS_NEW_FOLDER_SUCCESS,
                folder: extractFileAttributes(folder),
                parent
            })
        } catch (err) {
            if (err.response && err.response.status === HTTP_CODE_CONFLICT) {
                dispatch({
                    type: DOCUMENTS_NEW_FOLDER_FAILURE_DUPLICATE,
                    alert: {
                        message: `L'élément ${name} existe déjà, merci de choisir un nouveau nom.`,
                        level: ALERT_LEVEL_ERROR
                    }
                })
            } else {
                dispatch({
                    type: DOCUMENTS_NEW_FOLDER_FAILURE_GENERIC,
                    err,
                    alert: {
                        message: 'Impossible de créer le nouveau dossier ' + name,
                        level: ALERT_LEVEL_ERROR
                    }
                })
            }
            throw err
        }
    }
}

const testFolder = async(path, dirID) => {
    //test if homebook folders exist
    return await cozy.client.files.statByPath(path)
    .catch(err => {
        //create root folder
        const name = path.substring(path.lastIndexOf('/')+1)
        console.log('Create initial folder', name);
        return cozy.client.files.createDirectory({name, dirID})
        .then(folder => {
            folder.new = true
            return folder
        })
    })
}

export const documentsInitialize = () => {
    return async(dispatch, getState) => {
        dispatch({
            type: DOCUMENTS_INITIALIZE,
        })

        let useful = false

        try {
            //test if homebook folders exist
            const root = await testFolder(ROOT_PATH, COZY_ROOT_ID)
            for(const path of FOLDER_LIST) {
                const folder = await testFolder(path, root._id)
                if(folder.new) useful = true
            }

            if(useful) {
                dispatch(loadFolder(ROOT_PATH))
            }
            dispatch(gaugeInitialize())
            return dispatch({
                type: DOCUMENTS_INITIALIZE_SUCCESS,
                useful
            })
        } catch (err) {
            return dispatch({
                type: DOCUMENTS_INITIALIZE_FAILURE,
                err,
                alert: {
                    message: 'Impossible d\'initialiser Homebook',
                    level: ALERT_LEVEL_ERROR
                }
            })
        }
    }
}

const protectedFolders = DATA_LIST.map(item => item.path)

const getTypeOf = file => {
    return file.attributes.tags && file.attributes.tags[0];
}

export const documentsRemove = files => {
    return async dispatch => {
        dispatch({
            type: DOCUMENTS_REMOVE,
            files
        })
        const trashed = []
        try {
            for (const file of files) {
                if(file.path && protectedFolders.indexOf(file.path) !== -1) continue
                
                trashed.push(await cozy.client.files.trashById(file.id))
            }
            return dispatch({
                type: DOCUMENTS_REMOVE_SUCCESS,
                ids: trashed.map(f => f._id),
                types: trashed.map(f => getTypeOf(f)).filter(f => f !== undefined)
            })
        } catch (err) {
            return dispatch({
                type: DOCUMENTS_REMOVE_FAILURE,
                err,
                alert: {
                    message: 'Impossible de deplacer ces éléments dans la corbeille',
                    level: ALERT_LEVEL_ERROR
                }
            })
        }
    }
}

export const documentsRestore = files => {
    return async dispatch => {
        dispatch({
            type: DOCUMENTS_RESTORE,
            files
        })
        const restored = []
        try {
            for (const file of files) {
                restored.push(await cozy.client.files.restoreById(file.id))
            }
        } catch (err) {
            return dispatch({
                type: DOCUMENTS_RESTORE_FAILURE
            })
        }
        return dispatch({
            type: DOCUMENTS_RESTORE_SUCCESS,
            ids: files.map(f => f.id),
            types: restored.map(f => getTypeOf(f)).filter(f => f !== undefined)
        })
    }
}

export const documentsRename = (id, name) => {
    return async dispatch => {
        dispatch({
            type: DOCUMENTS_RENAME,
            id,
            name
        })
        try {
            const file = await cozy.client.files.updateAttributesById(id, {name})
            return dispatch({
                type: DOCUMENTS_RENAME_SUCCESS,
                file: extractFileAttributes(file),
            })
        } catch (err) {
            return dispatch({
                type: DOCUMENTS_RENAME_FAILURE,
                err
            })
        }
    }
}

export const documentsMove = (files, path) => {
    return async dispatch => {
        dispatch({
            type: DOCUMENTS_MOVE,
            files,
            path
        })
        const moved = []
        try {
            const parent = await cozy.client.files.statByPath(path)
            for (const file of files) {
                if(file.path && protectedFolders.indexOf(file.path) !== -1) continue                

                moved.push(await cozy.client.files.updateAttributesById(file.id, {dir_id: parent._id}))
            }
            dispatch(importerTree())
            return dispatch({
                type: DOCUMENTS_MOVE_SUCCESS,
                ids: moved.map(f => f._id)
            })
        } catch (err) {
            const data = {
                type: DOCUMENTS_MOVE_FAILURE,
                err
            }
            if(err.status === 409) {
                data.alert = {
                    message: "Déplacement intérrompu car un élément du même nom existe déjà dans le dossier cible."
                }
            }
            return dispatch(data)
        }
    }
}

export const documentsChangeType = (file, slug) => {
    return async dispatch => {
        dispatch({type: DOCUMENTS_CHANGE_TYPE, file, slug})
        try {
            const newFile = await cozy.client.files.updateAttributesById(file.id, {tags: [slug]})
            return dispatch({
                type: DOCUMENTS_CHANGE_TYPE_SUCCESS,
                file: extractFileAttributes(newFile)
            })
        } catch (err) {
            return dispatch({type: DOCUMENTS_CHANGE_TYPE_FAILURE, err})
        }
    }
}


