import { connect } from 'react-redux'
import { importerClick } from '../actions'
import Assistant from '../components/elements/Assistant'

import { getFileTypes } from '../scripts/fileTypes'

const mapStateToProps = state => {
    return {
        usedType: state.gauge.usedTypes,
        dataCreated: state.gauge.dataCreated,
        status: state.gauge.status
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onFileClick: (path, type) => {
            dispatch(importerClick(path, type))
        }
    }
}

const ConnectedAssistant = connect(
    mapStateToProps,
    mapDispatchToProps
)(Assistant)

export default ConnectedAssistant


