import { connect } from 'react-redux'
import { documentsInitialize, gaugeInitialize, alertShow, loadFolder } from '../actions'
import { ROOT_PATH } from '../constants'
import App from '../App'

const mapDispatchToProps = dispatch => {
    return {
        initilizeGauge: () => {
            dispatch(gaugeInitialize())
        },
        initilizeDocs: () => {
            dispatch(documentsInitialize())
        },
        alert: (msg, level) => {
            dispatch(alertShow(msg, level))
        },
        reloadDocs: () => {
            dispatch(loadFolder(ROOT_PATH))
        }
    }
}

const ConnectedApp = connect(
    null,
    mapDispatchToProps
)(App)

export default ConnectedApp


