import React, {Component} from 'react'
import classNames from 'classnames'

import {createOptionsFromArray} from '../../tools'
import button from '../../styles/buttons.scss'
import style from '../../styles/Modal.sass'

import { ALERT_LEVEL_ERROR } from '../../constants'

export default class InformationModal extends Component{

    constructor(props, context) {
        super(props, context)

        this.state = {
            open: false,
            close: false,
        }

        this.opening = setTimeout(() => {
            this.setState({open: true})
        })

        this.closing = setTimeout(() => {
            this.close()
        }, this.props.timeout || 2000)
    }

    componentWillUnmount() {
        if(this.closing){
            clearTimeout(this.closing)
        }

        if(this.opening){
            clearTimeout(this.opening)
        }

        if(this.anim){
            clearTimeout(this.anim)
        }
    }

    close() {
        this.setState({close: true, open: false})
        this.anim = setTimeout(() => {
            this.props.closePortal()
            this.props.onClose && this.props.onClose()
        }, 200)
    }

    onClose(e) {
        e.stopPropagation()
        this.close()
    }

    stopClosing(e) {
        e.stopPropagation()
        this.componentWillUnmount()
    }

    startClosing(e) {
        e.stopPropagation()

        this.closing = setTimeout(() => {
            this.close()
        }, 1000)
    }

    render() {
        const pos = this.props.position || 0

        return (
            <div className={classNames(style.popup, {[style.openActive]: this.state.open, [style.closeActive]: this.state.close, [style.error]: this.props.level === ALERT_LEVEL_ERROR})}
                onClick={e => e.stopPropagation()}
                onMouseEnter={e => this.stopClosing(e)}
                onMouseLeave={e => this.startClosing(e)}
                style={{top: (10+pos*10)+'%'}}>
                <div className={style.header}>
                    <h2>{this.props.title || 'Informations'}</h2>

                    <button
                        style={{fontSize: '20px'}}
                        onClick={e => this.onClose(e)}
                        className={classNames(button.button, button.stable, button.clear)}>
                        <i className="ion-ios-close-empty"></i>
                    </button>
                </div>
                <div className={style.content}>
                    <div>
                        {this.props.infos && this.props.infos.split('\n').map((item, key) => (
                            <p key={key}>{item}</p>
                        ))}
                    </div>
                </div>
            </div>
        )
    }
}


