import React from 'react'
import ReactDOM from 'react-dom'
import App from './containers/App'
import {Provider} from 'react-redux'
import getStore from './store'

import * as Actions from './actions'
import Exporter from './scripts/XMLDataFile'

import 'normalize-css/normalize.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'font-awesome/css/font-awesome.css'
import 'Ionicons/css/ionicons.css'

document.addEventListener('DOMContentLoaded', () => {
    const root = document.querySelector('[role=application]')
    const data = root.dataset

    console.log('Current Version:', '2.3.11');

    window
        .cozy
        .client
        .init({
            cozyURL: '//' + data.cozyDomain,
            token: data.cozyToken
        })

    window
        .cozy
        .bar
        .init({appName: data.cozyAppName, appEditor: data.cozyAppEditor, iconPath: data.cozyIconPath, lang: data.cozyLocale, replaceTitleOnMobile: true})

    const store = getStore()

    global.React = React
    global.store = store
    global.Actions = Actions
    global.Exporter = Exporter


    ReactDOM.render(
        <Provider store={store}>
            <App/>
        </Provider>, root)

})



