import { DOCTYPE_CONTACTS, ALERT_LEVEL_ERROR } from '../constants'

export const TRASH_EMPTY = "TRASH_EMPTY"
export const TRASH_EMPTY_SUCCESS = "TRASH_EMPTY_SUCCESS"
export const TRASH_EMPTY_FAILURE = "TRASH_EMPTY_FAILURE"
export const TRASH_CHANGE_VIEW_MODE = "TRASH_CHANGE_VIEW_MODE"
export const TRASH_SELECT_DOCUMENT = "TRASH_SELECT_DOCUMENT"
export const TRASH_SELECT_CONTACT = "TRASH_SELECT_CONTACT"
export const TRASH_SELECT_DATA = "TRASH_SELECT_DATA"
export const TRASH_SELECT_ALL = "TRASH_SELECT_ALL"

export const trashChangeViewMode = () => ({
    type: TRASH_CHANGE_VIEW_MODE
})

export const trashSelectContact = contact => ({
    type: TRASH_SELECT_CONTACT, contact
})

export const trashSelectDocument = document => ({
    type: TRASH_SELECT_DOCUMENT, document
})

export const trashSelectData = data => ({
    type: TRASH_SELECT_DATA, data
})

export const trashSelectAll = count => ({
    type: TRASH_SELECT_ALL, count
})

export const trashEmpty = () => {
    return async (dispatch, getState) => {
        dispatch({
            type: TRASH_EMPTY
        })
        try {
            await cozy.client.files.clearTrash()

            const contacts = getState().trash.contacts
            for(let contact of contacts) {
                await cozy.client.data.delete(DOCTYPE_CONTACTS, contact)
            }

            const data = getState().trash.data
            for(let d of data) {
                await cozy.client.data.delete(d.doctype, d)
            }
            return dispatch({
                type: TRASH_EMPTY_SUCCESS,
                alert: {
                    message: "La corbeille a bien été vidé"
                }
            })
        } catch (err) {
            return dispatch({
                type: TRASH_EMPTY_FAILURE,
                err,
                alert: {
                    message: "Impossible de vider la corbeille",
                    level: ALERT_LEVEL_ERROR
                }
            })
        }
    }
}


