import React, {Component} from 'react'
import modal from '../../styles/Modal.sass'

export const Modal = ({ closePortal, children, onClickOutside }) => {

    if (typeof children.type === 'function') {
        children = React.cloneElement(children, { closePortal });
    }

    const handleClickOutside = onClickOutside || closePortal

    return (
        <div className={modal.modal} onClick={handleClickOutside}>
            {children}
        </div>
    )
}

export default Modal


