import {connect} from 'react-redux'
import Portal from 'react-portal'
import {alertDismiss} from '../actions'
import InfomationModal from '../components/modals/InfomationModal'

const Wrapper = ({alerts, dismiss}) => (
    <div>
        {alerts.map((alert, key) => (
            <Portal key={key} isOpened>
                <InfomationModal
                    infos={alert.message}
                    level={alert.level || DEFAULT_ALERT_LEVEL}
                    onClose={e => dismiss(alert.id)}
                    position={key}/>
            </Portal>
        ))}
    </div>
)

const mapStateToProps = state => ({
    alerts: state.alerts
})
const mapDispatchToProps = dispatch => ({
    dismiss: id => dispatch(alertDismiss(id))
})

const Alerter = connect(
    mapStateToProps, 
    mapDispatchToProps
)(Wrapper)

export default Alerter


