import React, {Component} from 'react'

import DocumentView from '../../containers/DocumentView'
import Explorer from '../../containers/Explorer'

import { ROOT_PATH, TYPE_FOLDER, TYPE_FILE, STATUS_ERROR, STATUS_LOAD } from '../../constants'
import { getInfosFromPath } from '../../scripts/dataFields'

export default class Documents extends Component {
    
    constructor(props, context){
        super(props, context)

        this.newPath = null
    }

    componentWillMount () {
        this.newPath = this.createRealPath(this.props)
        
        if(this.newPath !== this.props.path || this.props.fileCount !== this.props.files.length) this.props.onOpenFolder(this.newPath)
    }

    componentWillReceiveProps (newProps) {
        this.newPath = this.createRealPath(newProps)
        
        if(newProps.status === STATUS_ERROR) {
            if(this.newPath === newProps.path) return
            return newProps.onOpenFolder(this.newPath)
        }
        if(newProps.status === STATUS_LOAD) return

        if(this.newPath !== newProps.path || newProps.fileCount !== newProps.files.length) newProps.onOpenFolder(this.newPath)
    }

    createRealPath(props) {
        const param = props.match.params.path
        let newPath = ROOT_PATH
        if (param) newPath += '/' + param
        return newPath
    }

    render() {
        const { lastType } = this.props

        if(lastType === TYPE_FILE){
            return (<DocumentView location={this.props.location} history={this.props.history} match={this.props.match}/>)
        } else if(lastType === TYPE_FOLDER){
            return (<Explorer path={this.newPath} location={this.props.location} history={this.props.history} match={this.props.match}/>)
        } else {
            return null
        }
    }
}


