import { connect } from 'react-redux'
import { importerDirectoryChange } from '../actions'
import Selector from '../components/modals/SelectorModal'

const mapStateToProps = state => {
    return {
        currentPath: state.importer.currentPath,
        documentsTree: state.importer.documentsTree,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onDirectoryChange: path => {
            dispatch(importerDirectoryChange(path))
        }
    }
}

const ConnectedSelector = connect(
    mapStateToProps,
    mapDispatchToProps
)(Selector)

export default ConnectedSelector


