import React from 'react'
import classNames from 'classnames'

import button from '../../styles/buttons.scss'
import style from '../../styles/ConfirmModal.sass'

import Modal from './Modal'

const ConfirmModal = ({ confirm, abort, children }) => {
    return (
        <Modal>
            <div className={style.main}>
                <div className={style.content}>
                    {children}
                </div>
                <div className={style.actions}>
                    <button className={classNames(button.button, button.default)} onClick={e => abort()}>
                        ANNULER
                    </button>
                    <button onClick={e => confirm()} className={classNames(button.button, button.assertive)}>
                        SUPPRIMER
                    </button>
                </div>
            </div>
        </Modal>
    )
}

export default ConfirmModal


