import moment from 'moment'
import _ from 'lodash'
import { extractFileAttributes, getDataElements } from '../tools'

import {
    DOCTYPE_CONTACTS,
    DOCTYPE_DIAGNOSIS,
    DOCTYPE_EQUIPMENTS,
    DOCTYPE_HOMEDATA,
    DOCTYPE_MAINTENANCES,
    DOCTYPE_META,
    DOCTYPE_RECALLS,
    DOCTYPE_WORKS,
    DOCTYPE_NEWS,
    DATA_LIST,
    ROOT_PATH
} from '../constants'

export const LOAD_DOCS = "LOAD_DOCS"
export const LOAD_DOCS_SUCCESS = "LOAD_DOCS_SUCCESS"
export const LOAD_DOCS_FAILURE = "LOAD_DOCS_FAILURE"
export const LOAD_RECALLS = "LOAD_RECALLS"
export const LOAD_RECALLS_SUCCESS = "LOAD_RECALLS_SUCCESS"
export const LOAD_RECALLS_FAILURE = "LOAD_RECALLS_FAILURE"
export const LOAD_CONTACTS = "LOAD_CONTACTS"
export const LOAD_CONTACTS_SUCCESS = "LOAD_CONTACTS_SUCCESS"
export const LOAD_CONTACTS_FAILURE = "LOAD_CONTACTS_FAILURE"
export const LOAD_DATA = "LOAD_DATA"
export const LOAD_DATA_SUCCESS = "LOAD_DATA_SUCCESS"
export const LOAD_DATA_FAILURE = "LOAD_DATA_FAILURE"
export const LOAD_TRASH = "LOAD_TRASH"
export const LOAD_TRASH_SUCCESS = "LOAD_TRASH_SUCCESS"
export const LOAD_TRASH_FAILURE = "LOAD_TRASH_FAILURE"
export const LOAD_FILE_SUCCESS = "LOAD_FILE_SUCCESS"
export const LOAD_NEWS = "LOAD_NEWS"
export const LOAD_NEWS_SUCCESS = "LOAD_NEWS_SUCCESS"
export const LOAD_NEWS_FAILURE = "LOAD_NEWS_FAILURE"

export const loadFolder = (path=ROOT_PATH) => {
    return async(dispatch, getState) => {
        dispatch({
            type: LOAD_DOCS,
            path: path
        })
        try {
            const element = await cozy.client.files.statByPath(path)

            const parentId = element.attributes.dir_id
            const parent = !!parentId && await cozy.client.files.statById(parentId)
            .catch(ex => {
                if (ex.status === 403) {
                    console.warn('User don\'t have access to parent folder')
                } else {
                    throw ex
                }
            })
            if(element.attributes.type === 'directory') {
                const contents = element.relationships.contents
                // folder.relations('contents') returns null when the trash is empty
                // the filter call is a temporary fix due to a cozy-client-js bug
                let files = element.relations('contents').filter(f => f !== undefined) || []
                const fileCount = contents.meta.count || 0
                if(fileCount > files.length) {
                    //load more files
                    console.log('More files', element._id);
                    files = await loadFileRec(element._id, files, fileCount)
                }

                return dispatch({
                    type: LOAD_DOCS_SUCCESS,
                    folder: Object.assign(extractFileAttributes(element), {
                        parent: extractFileAttributes(parent)
                    }),
                    fileCount,
                    files: files.map(c => extractFileAttributes(c))
                })
            } else {
                return dispatch({
                    type: LOAD_FILE_SUCCESS,
                    file: Object.assign(extractFileAttributes(element), {
                        parent: extractFileAttributes(parent),
                        path: parent.attributes.path + '/' + element.attributes.name
                    })
                })
            }

        } catch (err) {
            return dispatch({
                type: LOAD_DOCS_FAILURE,
                error: err
            })
        }
    }
}

const loadFileRec = async(id, files, count) => {
    const element = await cozy.client.files.statById(id, true, {skip: files.length})
    const moreFiles = element.relations('contents').filter(f => f !== undefined) || []
    files = files.concat(moreFiles)

    if(count > files.length) {
        files = await loadFileRec(id, files, count)
    }
    return files  
}

export const loadRecalls = () => {
    return async(dispatch, getState) => {
        dispatch({
            type: LOAD_RECALLS
        })
        try {
            const recalls = await getDataElements(DOCTYPE_RECALLS)
            recalls.map(item => ({
                ...item,
                deadline: moment(item.deadline, 'DD/MM/YYYY'),
                alert: moment(item.alert, 'DD/MM/YYYY')
            }));

            return dispatch({
                type: LOAD_RECALLS_SUCCESS,
                recalls
            })
        } catch (err) {
            return dispatch({
                type: LOAD_RECALLS_FAILURE,
                error: err
            })
        }
    }
}

export const loadContacts = () => {
    return async(dispatch, getState) => {
        dispatch({
            type: LOAD_CONTACTS
        })
        try {
            const result = await getDataElements(DOCTYPE_CONTACTS)
            const noTrashContacts = result.filter(c => !c.trash)

            return dispatch({
                type: LOAD_CONTACTS_SUCCESS,
                contacts: noTrashContacts
            })
        } catch (err) {
            return dispatch({
                type: LOAD_CONTACTS_FAILURE,
                error: err
            })
        }
    }
}


export const loadNews = () => {
    return async(dispatch, getState) => {
        dispatch({
            type: LOAD_NEWS
        })
        try {
            const result = await getDataElements(DOCTYPE_NEWS)

            return dispatch({
                type: LOAD_NEWS_SUCCESS,
                news: result
            })
        } catch (err) {
            return dispatch({
                type: LOAD_NEWS_FAILURE,
                error: err
            })
        }
    }
}


export const loadData = (doctype = DOCTYPE_DIAGNOSIS) => {
    return createLoader(LOAD_DATA, LOAD_DATA_SUCCESS, LOAD_DATA_FAILURE, doctype)
}

const createLoader = (action, actionSuccess, actionFailure, doctype) => {
    return async(dispatch, getState) => {
        dispatch({
            type: action
        })
        try {
            const data = await getDataElements(doctype)
            const noTrashData = data.filter(d => !d.trash)

            return dispatch({
                type: actionSuccess,
                data: noTrashData.map(item => {
                    item.doctype = doctype
                    return item
                }),
                doctype
            })
        } catch (err) {
            return dispatch({
                type: actionFailure,
                error: err
            })
        }
    }
}

