export const loadTrash = () => {
    return async(dispatch, getState) => {
        dispatch({
            type: LOAD_TRASH,
        })
        try {
            const files = await cozy.client.files.listTrash()
            const result = await getDataElements(DOCTYPE_CONTACTS)
            const contacts = result.filter(c => c.trash)

            const data = []
            for(const infos of DATA_LIST) {
                const result = await getDataElements(infos.doctype)
                const newData = result.filter(c => c.trash).map(d => {d.doctype = infos.doctype; return d})
                data.push(...newData)
            }

            return dispatch({
                type: LOAD_TRASH_SUCCESS,
                files: files.map(c => extractFileAttributes(c)),
                contacts,
                data
            })
        } catch (err) {
            return dispatch({
                type: LOAD_TRASH_FAILURE,
                error: err
            })
        }
    }
}



