import React, {PureComponent, Component} from 'react';
import style from '../../styles/Footer.sass';

import { DOCTYPE_META } from '../../constants'

export default class Footer extends Component {
    constructor(props, context) {
        super(props, context)

        this.state = {
            actors: null
        }
    }

    componentWillMount() {
        getMeta(DOCTYPE_META)
        .then(data => {
            this.setState({
                actor: data.map(d => d.actor).shift()
            })
        })

    }

    render() {
        return (
            <footer className={style.main}>
                <div className={style.content}>
                    <span>HOMEBOOK est un service proposé par </span>
                    <img src="/images/logo.edf.png" alt="logo_edf"/>
                    {this.state.actor && (
                        <span> pour </span>
                    )}
                    {this.state.actor && (
                        <img src={`/images/logo.${this.state.actor}.png`} alt={'logo '+this.state.actor}/>
                    )}
                    <br/>                    
                    <span>V{'2.3.11'}</span>
                </div>
            </footer>
        )
    }
}

export const getMeta = async() => {
    try {
        const indexRef = await cozy.client.data.defineIndex(DOCTYPE_META, ['name'])
        const result = cozy.client.data.query(indexRef, {
            selector: {
                "name": "actors"
            }
        })
        return result
    } catch (error) {
        return []   
    }
}


