import {connect} from 'react-redux'
import {
    documentsChangeViewMode,
    documentsDownload,
    documentsNewFolder,
    documentsRemove,
    documentsSelect,
    importerClick,
    documentsChangeFilter,
    dataSelect,
    dataRemove,
    documentsRename,
    documentsMove,
    loadData,
} from '../actions'
import Explorer from '../components/elements/Explorer'

import {SORT_DATE, SORT_NAME} from '../constants'

const sortFiles = (files, filter, need) => {
    return files.sort((a, b) => {
        let aa, bb
        if (filter === SORT_DATE) {
            aa = new Date(b.updated_at).getTime()
            bb = new Date(a.updated_at).getTime()
        } else if (filter === SORT_NAME) {
            aa = a.name.toLowerCase()
            bb = b.name.toLowerCase()
        }
        let adir = a.type === 'directory' 
        let bdir = b.type === 'directory' 

        if(adir && !bdir) {
            return -1
        } else if(!adir && bdir) {
            return 1
        } else {
            if (aa < bb) 
                return -1
            if (bb < aa) 
                return 1
        }

        return 0
    })
}

const mapStateToProps = state => {
    return {
        files: state.documents.needSort ? sortFiles(state.documents.files, state.documents.filter): state.documents.files,
        status: state.documents.fetchStatus,
        folder: state.documents.folder,
        path: state.documents.path,
        fileCount: state.documents.fileCount,
        viewMode: state.viewMode,
        selectedCount: state.documents.selectedCount,
        filter: state.documents.filter,
        data: state.data.data,
        dataStatus: state.data.fetchStatus,
        dataCount: state.data.dataCount,
        selectedDataCount: state.data.selectedCount
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onLoadData: doctype => {
            dispatch(loadData(doctype))
        },
        onChangeViewMode: () => {
            dispatch(documentsChangeViewMode())
        },
        onNewFolder: (name = '') => {
            if (!name.match(/[/\\:\*\?|<>]/)) //filter folder name
                dispatch(documentsNewFolder(name))
        },
        onDownload: docs => {
            dispatch(documentsDownload(docs))
        },
        onRemove: docs => {
            dispatch(documentsRemove(docs))
        },
        onDownloadAll: (docs) => {
            dispatch(documentsDownload(docs))
        },
        onSelect: document => {
            dispatch(documentsSelect(document))
        },
        onImporterClick: path => {
            dispatch(importerClick(path))
        },
        onChangeFilter: filter => {
            dispatch(documentsChangeFilter(filter))
        },
        onSelectData: data => {
            dispatch(dataSelect(data))
        },
        onRemoveData: (doctype, data) => {
            dispatch(dataRemove(doctype, data))
        },
        onRename: (id, name) => {
            dispatch(documentsRename(id, name))
        },
        onMoveFile: (files, path) => {
            dispatch(documentsMove(files, path))
        }
    }
}

const ConnectedExplorer = connect(mapStateToProps, mapDispatchToProps)(Explorer)

export default ConnectedExplorer


