import {
    combineReducers
} from 'redux'

import {
    LOAD_TRASH,
    LOAD_TRASH_SUCCESS,
    LOAD_TRASH_FAILURE,
    TRASH_EMPTY,
    TRASH_EMPTY_SUCCESS,
    TRASH_EMPTY_FAILURE,
    DOCUMENTS_REMOVE_SUCCESS,
    DOCUMENTS_REMOVE_FAILURE,
    DOCUMENTS_RESTORE,
    DOCUMENTS_RESTORE_SUCCESS,
    DOCUMENTS_RESTORE_FAILURE,
    CONTACTS_RESTORE,
    CONTACTS_REMOVE_SUCCESS,
    CONTACTS_REMOVE_FAILURE,
    CONTACTS_RESTORE_SUCCESS,
    CONTACTS_RESTORE_FAILURE,
    TRASH_SELECT_CONTACT,
    TRASH_SELECT_DOCUMENT,
    TRASH_SELECT_DATA,
    TRASH_SELECT_ALL,
    DATA_REMOVE_SUCCESS,
    DATA_REMOVE_FAILURE,
    DATA_RESTORE,
    DATA_RESTORE_SUCCESS,
    DATA_RESTORE_FAILURE,
} from '../actions'

import { STATUS_LOAD, STATUS_DONE, STATUS_ERROR } from '../constants'

const elementCount = (state = 0, action) => {
    switch (action.type) {
        case LOAD_TRASH_SUCCESS:
            return action.files.length + action.contacts.length + action.data.length
        case CONTACTS_REMOVE_SUCCESS:
        case DOCUMENTS_REMOVE_SUCCESS:
        case DATA_REMOVE_SUCCESS:
            return state + 1
        case DOCUMENTS_RESTORE_SUCCESS:
        case CONTACTS_RESTORE_SUCCESS:
        case DATA_RESTORE_SUCCESS:
            return state - action.ids.length
        case TRASH_EMPTY_SUCCESS:
            return 0
        default:
            return state
    }
}

const selectedCount = (state = 0, action) => {
    switch (action.type) {
        case LOAD_TRASH_SUCCESS:
        case DOCUMENTS_RESTORE_SUCCESS:
        case CONTACTS_RESTORE_SUCCESS:
        case DATA_RESTORE_SUCCESS:
        case TRASH_EMPTY_SUCCESS:
            return 0
        case TRASH_SELECT_CONTACT:
            if(action.contact.selected)
                return state - 1
            else
                return state + 1
        case TRASH_SELECT_DOCUMENT:
            if(action.document.selected)
                return state - 1
            else
                return state + 1
        case TRASH_SELECT_DATA:
            if(action.data.selected)
                return state - 1
            else
                return state + 1
        case TRASH_SELECT_ALL:
            return action.count
        default:
            return state
    }
}

const files = (state = [], action) => {
    switch (action.type) {
        case LOAD_TRASH_SUCCESS:
            return action.files
        case DOCUMENTS_RESTORE_SUCCESS:
            return state.filter(f => action.ids.indexOf(f._id) !== -1)
        case TRASH_EMPTY_SUCCESS:
            return []
        case TRASH_SELECT_DOCUMENT:
            const updated = state.map(f => {
                if(f.id === action.document.id) f.selected = !f.selected
                return f
            })
            return [...updated]
        case TRASH_SELECT_ALL:
            const selected = state.map(f => {
                f.selected = !f.selected
                return f
            })
            return [...selected]
        default:
            return state
    }
}

const contacts = (state = [], action) => {
    switch (action.type) {
        case LOAD_TRASH_SUCCESS:
            return action.contacts
        case CONTACTS_RESTORE_SUCCESS:
            return state.filter(f => action.ids.indexOf(f._id) === -1)
        case TRASH_EMPTY_SUCCESS:
            return []
        case TRASH_SELECT_CONTACT:
            const updated = state.map(f => {
                if(f._id === action.contact._id) f.selected = !f.selected
                return f
            })
            return [...updated]
        case TRASH_SELECT_ALL:
            const selected = state.map(f => {
                f.selected = !f.selected
                return f
            })
            return [...selected]
        default:
            return state
    }
}

const data = (state = [], action) => {
    switch (action.type) {
        case LOAD_TRASH_SUCCESS:
            return action.data
        case DATA_RESTORE_SUCCESS:
            return state.filter(f => action.ids.indexOf(f._id) === -1)
        case TRASH_EMPTY_SUCCESS:
            return []
        case TRASH_SELECT_DATA:
            const updated = state.map(f => {
                if(f._id === action.data._id) f.selected = !f.selected
                return f
            })
            return [...updated]
        case TRASH_SELECT_ALL:
            const selected = state.map(f => {
                f.selected = !f.selected
                return f
            })
            return [...selected]
        default:
            return state
    }
}


const fetchStatus = (state = STATUS_LOAD, action) => {
    switch (action.type) {
        // there's a trick here : we set the fetchStatus to STATUS_LOAD
        // on initial state so that the loading spinner is only showed
        // when the app is launched or when the user use the back button
        case TRASH_EMPTY: // we temporarily display the spinner when working in the trashed
        case DOCUMENTS_RESTORE: // TODO: display a spinner in the confirm modal instead
        case CONTACTS_RESTORE:
            return STATUS_LOAD
        case LOAD_TRASH_SUCCESS:
        case TRASH_EMPTY_SUCCESS:
        case TRASH_EMPTY_FAILURE:
        case DOCUMENTS_RESTORE_SUCCESS:
        case DOCUMENTS_RESTORE_FAILURE:
        case CONTACTS_RESTORE_SUCCESS:
        case CONTACTS_RESTORE_FAILURE:
            return STATUS_DONE
        case LOAD_TRASH_FAILURE:
            return STATUS_ERROR
        default:
            return state
    }
}

const lastFetch = (state = null, action) => {
    switch (action.type) {
        case LOAD_TRASH_SUCCESS:
        case LOAD_TRASH_FAILURE:
            return Date.now()
        default:
            return state
    }
}

export default combineReducers({
    elementCount,
    selectedCount,
    files,
    contacts,
    data,
    fetchStatus,
    lastFetch,
})


