import {
    combineReducers
} from 'redux'

import {
    LOAD_DATA,
    LOAD_DATA_SUCCESS,
    LOAD_DATA_FAILURE,
    DATA_NEW_SUCCESS,
    DATA_REMOVE,
    DATA_REMOVE_SUCCESS,
    DATA_REMOVE_FAILURE,
    DATA_SAVE,
    DATA_SAVE_SUCCESS,
    DATA_SAVE_FAILURE,
    DATA_RESTORE_SUCCESS,
    DATA_SELECT,
    DATA_LOAD,
    DATA_LOAD_SUCCESS,
    DATA_LOAD_FAILURE,
} from '../actions'

import {
    DOCTYPE_DIAGNOSIS,
    DOCTYPE_EQUIPMENTS,
    DOCTYPE_HOMEDATA,
    DOCTYPE_MAINTENANCES,
    DOCTYPE_WORKS,
    STATUS_DONE,
    STATUS_ERROR,
    STATUS_LOAD
} from '../constants'

export const data = (state = [], action) => {
    switch (action.type) {
        case LOAD_DATA_SUCCESS:
            return action.data
        case DATA_NEW_SUCCESS:
            return [...state, action.data]
        case DATA_REMOVE_SUCCESS:
            return state.filter(f => action.ids.indexOf(f._id) === -1)
        case DATA_SAVE_SUCCESS:
            const withoutData = state.filter(f => f._id !== action.data._id)
            return [...withoutData, action.data]
        case DATA_SELECT:
            const updated = state.map(f => {
                if(f._id === action.data._id) f.selected = !f.selected
                return f
            })
            return [...updated]
        default:
            return state
    }
}

export const doctype = (state = null, action) => {
    switch (action.type) {
        case LOAD_DATA_SUCCESS:
        case DATA_LOAD_SUCCESS:
            return action.doctype
        default:
            return state
    }
}

export const dataCount = (state = 0, action) => {
    switch (action.type) {
        case LOAD_DATA_SUCCESS:
            return action.data.length
        case DATA_REMOVE_SUCCESS:
            return state - action.ids.length
        case DATA_RESTORE_SUCCESS:
            return state + action.ids.length
        case DATA_NEW_SUCCESS:
            return state + 1
        default:
            return state
    }
}

export const selectedCount = (state=0, action) => {
    switch (action.type) {
        case LOAD_DATA_SUCCESS:
        case DATA_REMOVE_SUCCESS:
            return 0
        case DATA_SELECT:
           if(action.data.selected)
                return state - 1
            else
                return state + 1
        default:
            return state
    }
}

export const current = (state=null, action) => {
    switch (action.type) {
        case DATA_LOAD_SUCCESS:
        case DATA_SAVE_SUCCESS:
            return action.data
        default:
            return state
    }
}

const fetchStatus = (state = STATUS_DONE, action) => {
    switch (action.type) {
        case LOAD_DATA:
        case DATA_LOAD:
        case DATA_REMOVE:
        case DATA_SAVE:
            return STATUS_LOAD
        case LOAD_DATA_SUCCESS:
        case DATA_REMOVE_SUCCESS:
        case DATA_SAVE_SUCCESS:
        case DATA_LOAD_SUCCESS:
            return STATUS_DONE
        case DATA_REMOVE_SUCCESS:
        case LOAD_DATA_FAILURE:
        case DATA_SAVE_FAILURE:
        case DATA_LOAD_FAILURE:
            return STATUS_ERROR
        default:
            return state
    }
}

export default combineReducers({
    selectedCount,
    data,
    doctype,
    dataCount,
    current,
    fetchStatus
})



