import { connect } from 'react-redux'
import { trashChangeViewMode, trashEmpty, documentsRestore, contactsRestore, loadTrash, trashSelectContact, trashSelectDocument, trashSelectAll, dataRestore, trashSelectData } from '../actions'
import Trash from '../components/elements/Trash'

import confirm from '../scripts/confirm'

import React from 'react'
import ConfirmModal from '../components/modals/ConfirmModal'

const modal = (
    <ConfirmModal>
        <p>
            Voulez-vous vider la corbeille ?
        </p>
    </ConfirmModal>
)

const mapStateToProps = state => {
    return {
        files: state.trash.files,
        contacts: state.trash.contacts,
        data: state.trash.data,
        count: state.trash.elementCount,
        viewMode: state.viewMode,
        selectedCount: state.trash.selectedCount,
        status: state.trash.fetchStatus
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onEmpty: () => {
            confirm(modal)
            .then(() => dispatch(trashEmpty()))
            .catch(err => {})
        },
        onRestoreDocuments: (docs) => {
            dispatch(documentsRestore(docs))
        },
        onRestoreContacts: (contacts) => {
            dispatch(contactsRestore(contacts))
        },
        onRestoreData: (data) => {
            dispatch(dataRestore(data))
        },
        onSelectContact: contact => {
            dispatch(trashSelectContact(contact))
        },
        onSelectFile: file => {
            dispatch(trashSelectDocument(file))
        },
        onSelectData: data => {
            dispatch(trashSelectData(data))
        },
        onLoadTrash: () => {
            dispatch(loadTrash())
        },
        onChangeViewMode: () => {
            dispatch(trashChangeViewMode())
        },
        onSelectAll: count => {
            dispatch(trashSelectAll(count))
        }
    }
}

const ConnectedTrash = connect(
    mapStateToProps,
    mapDispatchToProps
)(Trash)

export default ConnectedTrash


