import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import classNames from 'classnames'

import PDFViewer from '../utils/PDFViewer'
import TXTViewer from '../utils/TXTViewer'
import Loader from './Loader'
import Page from './Page'
import ToolBoxOptions from './ToolBoxOptions'
import Modal from '../modals/Modal'
import TypeChanger from '../utils/TypeChanger'

import style from '../../styles/Documents.sass'
import button from '../../styles/buttons.scss'

import { getDisplayName } from '../../scripts/fileTypes'
import { STATUS_DONE, STATUS_LOAD, STATUS_ERROR } from '../../constants'

const PDF = 'application/pdf'
const TEXT = 'text/plain'
const IMAGE = 'image/'

export default class DocumentView extends Component{

    constructor(props, context) {
        super(props, context)

        this.state = {
            url: '',
            loading: true,
            modalOpen: true,
            zoom: 0.1,
            changeType: false,
            imgLoad: true
        }

        this.type = undefined

        this.touch = {
            start: false,
            lastDistance: 0
        }

        this.renderMethod = this.renderSimple.bind(this)
    }

    componentWillMount() {
        cozy.client.files.getDownloadLinkById(this.props.current.id)
        .then(url => cozy.client.fullpath(url))
        .then(url => this.setState({url, loading: false}))
        
        const types = {
            [PDF]: this.renderPDF,
            [TEXT]: this.renderText,
            [IMAGE]: this.renderImage
        }

        for(const mime of Object.keys(types)) {
            if(this.props.current.mime.indexOf(mime) !== -1){
                this.renderMethod = types[mime].bind(this)
                this.type = mime
                break
            }
        }

        if(this.type === IMAGE) {
            const windowWidth = window.innerWidth
            const imageWidth = this.props.current.metadata.width
            this.state.zoom = Math.min(Math.max(windowWidth / imageWidth, 0.1), 1)
        } else if(this.type === undefined) {
            this.props.onAlert('Impossible de lire ce type de fichier, veuillez le télécharger afin de le lire sur votre ordinateur')
        }
    }

    componentWillReceiveProps(newProps){
    }

    download() {
        this.props.onDownload(this.props.current)
    }

    open() {
        if(!this.state.url) return 

        const win = window.open(this.state.url, '_blank')
        win.focus()
    }

    remove() {
        this.props.onRemove(this.props.current)
        .then(() => this.props.history.goBack())
    }

    renderSimple() {
        return (
            <div>
                <div>URL du fichier : <a href={this.state.url}>{this.state.url}</a></div>
            </div>
        )
    }

    renderText() {
        return (
            <TXTViewer url={this.state.url}/>
        )
    }

    renderImage() {
        const w = this.props.current.metadata.width * this.state.zoom
        const h = this.props.current.metadata.height * this.state.zoom

        return (
            <div ref="imagebox" style={{overflow: 'auto', flex: 1, display:'inline-block'}} onTouchStart={e => this.handleTouchStart(e)} onTouchEnd={e => this.handleTouchStop(e)} onTouchMove={e => this.handleTouchMove(e)}>
                <Loader display={this.state.imgLoad}/>
                <img style={{width: `${w}px`, height: `${h}px`}} src={this.state.url} alt={this.props.current.name} onWheel={e => this.imageZoom(e)} onLoad={e => this.setState({imgLoad: false})}/>
            </div>
        )
    }

    componentDidUpdate() {
        if(this.refs.imagebox && this.type === IMAGE){
            const node = this.refs.imagebox

            const maxScrollX = node.scrollWidth - node.clientWidth
            const maxScrollY = node.scrollHeight - node.clientHeight

            node.scrollLeft = maxScrollX / 2
            node.scrollTop = maxScrollY / 2
        }
    }

    imageZoom(e) {
        e.preventDefault()
        e.stopPropagation()

        if(e.deltaY > 0){
            //unzoom
            if(this.state.zoom <= 0.21) return this.setState({zoom: 0.1})
            this.setState({zoom: this.state.zoom-0.2})
        } else {
            //zoom
            if(this.state.zoom >= 4) return this.setState({zoom: 4})
            this.setState({zoom: this.state.zoom+0.2})
        }
    }

    getPointFromTouch(touch, element) {
        const rect = element.getBoundingClientRect(); 
        return {
            x: touch.clientX - rect.left,
            y: touch.clientY - rect.top,
        }
    }

    getDistance(a, b) {
        return Math.sqrt(Math.pow( a.x - b.x, 2) + Math.pow(a.y - b.y, 2))
    }

    handleTouchMove(e) {
        if(!this.touch.start || e.touches.length < 2) return

        const a = this.getPointFromTouch(e.touches[0], this.refs.imagebox)
        const b = this.getPointFromTouch(e.touches[1], this.refs.imagebox)
        const dist = this.getDistance(a,b)
        const Ddist = dist - this.touch.lastDistance
        this.touch.lastDistance = dist

        if(Ddist < -10 || Ddist > 10 ){
            const newZoom = this.state.zoom + (Ddist / 600)
            if(newZoom <= 0.1 || newZoom >= 5) return
            console.log('New zoom', newZoom);
            this.setState({zoom: newZoom})
        }
    }

    handleTouchStart(e) {
        if(this.touch.start || e.touches.length < 2) return
        this.touch.start = true

        const a = this.getPointFromTouch(e.touches[0], this.refs.imagebox)
        const b = this.getPointFromTouch(e.touches[1], this.refs.imagebox)
        this.touch.lastDistance = this.getDistance(a,b)
    }

    handleTouchStop() {
        this.touch.start = false
    }

    renderPDF() {
        return (
            <PDFViewer url={this.state.url}/>
        )
    }

    createBreadcrums() {
        const displayPath = this.props.location.pathname.substring(1);
        const foldersSplit = displayPath.split('/');
        const folders = [];

        if (foldersSplit.length > 1) {
            var root = foldersSplit[0];
            folders.push({
                path: root,
                fullPath: '/' + root
            });

            if (foldersSplit.length > 3) {
                var dot = foldersSplit[foldersSplit.length - 3];
                folders.push({
                    path: '/...',
                    fullPath: '/' + foldersSplit
                        .slice(0, foldersSplit.length - 2)
                        .join('/')
                });

                var f = foldersSplit[foldersSplit.length - 2];
                folders.push({
                    path: '/' + f,
                    fullPath: '/' + foldersSplit
                        .slice(0, foldersSplit.length - 1)
                        .join('/')
                });
            } else if (foldersSplit.length > 2) {
                var f = foldersSplit[foldersSplit.length - 2];
                folders.push({
                    path: '/' + f,
                    fullPath: '/' + foldersSplit
                        .slice(0, foldersSplit.length - 1)
                        .join('/')
                });
            }

        }
        var lastFolder = (foldersSplit.length > 1
            ? '/'
            : '') + foldersSplit[foldersSplit.length - 1];

        return (
            <span className={style.breadcrums}>
                {folders.map((el, key) => (
                    <Link key={key} to={el.fullPath}>{el.path}</Link>
                ))}
                <strong>{lastFolder}</strong>
            </span>
        )
    }
    
    render() {
        let subtitle = null
        if(this.state.changeType) {
            subtitle = (
                <TypeChanger type={this.props.current.tags[0]}
                    onCancel={() => this.setState({changeType: false})}
                    onChange={type => {this.setState({changeType: false}); this.props.onChangeType(this.props.current, type)}}/>
            )
        } else {
            subtitle = this.props.current.tags && getDisplayName(this.props.current.tags[0])
        }

        const toolbox = (
            <ToolBoxOptions>
                <ul>
                    <li onClick={() => {
                        this.setState({changeType: true})
                    }}>
                        <i className="ion-ios-cloud-download-outline">CHANGE TYPE</i>
                    </li>
                </ul>
            </ToolBoxOptions>
        )
        const download = (<button onClick={() => this.download()} className={classNames(button.button, button.stable)} title="Télécharger"><i className="ion-ios-cloud-download-outline"></i><span className={button.text}>TELECHARGER</span></button>)
        const open = (<button onClick={() => this.open()} className={classNames(button.button, button.stable)} title="Ouvrir"><i className="ion-ios-book-outline"></i><span className={button.text}>OUVRIR</span></button>)
        const remove = (<button onClick={() => this.remove()} className={classNames(button.button, button.assertive)} title="Supprimer"><i className="ion-ios-trash-outline"></i><span className={button.text}>SUPPRIMER</span></button>)

        const { loading } = this.state

        return (
            <Page title={this.createBreadcrums()} 
                subtitle={!loading && status != STATUS_ERROR && subtitle} 
                actions={[download, open, remove, toolbox]}
                onBackPress={() => this.props.history.goBack()}
                flex={this.props.current.mime.indexOf(IMAGE) !== -1}>

                <Loader display={loading}/>

                {!loading && this.renderMethod()} 

            </Page>
        )
    }
}


