import React, { Component } from 'react'
import _ from 'lodash'
import classNames from 'classnames'
import Portal from 'react-portal'

import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'
import Page from './Page'

import {getFileTypes} from '../../scripts/fileTypes'
import style from '../../styles/Assistant.sass'

import { DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS } from '../../constants'
import { getDataElements } from '../../tools'

const Doctypes = [
    {
        doctype: DOCTYPE_DIAGNOSIS,
        display: 'Ajouter un diagnostic',
        path: '/assistant/diagnosis',
        unique: false
    },
    {
        doctype: DOCTYPE_EQUIPMENTS,
        display: 'Ajouter un équipement',
        path: '/assistant/equipments',
        unique: false
    },
    {
        doctype: DOCTYPE_HOMEDATA,
        display: 'Ajouter une donnée de propiété',
        slug: 'homedata',
        path: '/assistant/homedata',
        unique: true
    },
    {
        doctype: DOCTYPE_MAINTENANCES,
        display: 'Ajouter un entretien',
        path: '/assistant/maintenances',
        unique: false
    },
    {
        doctype: DOCTYPE_WORKS,
        display: 'Ajouter des travaux',
        path: '/assistant/works',
        unique: false
    }
]

export default class Assistant extends Component {

    constructor(props) {
        super(props)

        this.fileTypes = getFileTypes()

        this.legend = this.legend.bind(this)

        this.state = {
            open: false,
            search: ''
        }
    }

    componentWillMount() {
        this.computeWidth(this.props)            
    }

    componentWillReceiveProps(newProps) {
        this.computeWidth(newProps)                
    }

    computeWidth(props) {
        this.width = 0
        if(props.usedType) {
            const used = props.usedType.map(f => f.type)
            this.width = (used.length / this.fileTypes.length) * 100
        }
    }

    clickDocument(key, location) {
        this.props.onFileClick(location, key)
        this.setState({open: true}) 
    }

    legend() {
        return (
            <div>
                <h3>Légende</h3>
                <ul className={style.legend}>
                    <li>
                        <div className={style.legendRow}>
                            <div className={style.legendCheck}></div>
                            <div className={style.legendName}>Element unique à ajouter</div>
                        </div>
                    </li>
                    <li>
                        <div className={style.legendRow}>
                            <div className={classNames(style.legendCheck, style.checked)}>
                                <i className="ion-ios-checkmark-outline"></i>
                            </div>
                            <div className={style.legendName}>Element unique déjà ajouté, vous ne pouvez pas en ajouter d'autre</div>
                        </div>
                    </li>
                </ul>
                <ul className={style.legend}>
                    <li>
                        <div className={classNames(style.legendRow, style.multi)}>
                            <div className={style.legendCheck}></div>
                            <div className={style.legendName}>Element, peut être ajouté plusieurs fois</div>
                        </div>
                    </li>
                    <li>
                        <div className={classNames(style.legendRow, style.multi)}>
                            <div className={classNames(style.legendCheck, style.checked)}>
                                <i className="ion-ios-checkmark-outline"></i>
                            </div>
                            <div className={style.legendName}>Element ajouté au moins une fois</div>
                        </div>
                    </li>
                </ul>
            </div>
        )
    }

    render() {

        let docs = this.fileTypes
        if(this.state.search !== '') docs = this.fileTypes.filter(f => f.name.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1)

        return (
            <Page title="Assistant de données">
                <div className={style.main}>
                    <div className={style.gauge} title={`Jauge : ${this.width.toFixed(1)}%`} style={{marginTop: this.state.marginTop+'px'}}>
                        <div className={style.progress} style={{height: this.width+'%'}}></div>
                    </div>
                    <div className={style.entries}>
                        <h3>Ajouter une donnée</h3>
                        <ul className={style.list}>
                            {Doctypes.map((item, key) => (
                                <li key={key}>
                                    <Row title={item.display} check={_.find(this.props.dataCreated, t => t.doctype === item.doctype)} unique={item.unique}
                                        onAdd={e => {
                                            if(item.unique) {
                                                getDataElements(item.doctype)
                                                .then(res => {
                                                    if(res.length > 0) {
                                                        this.props.history.push(`/data/${item.slug}/${res[0]._id}`)
                                                    }
                                                })
                                            } else {
                                                this.props.history.push(item.path)
                                            }
                                        }}/>
                                </li>
                            ))}
                        </ul>
                        <div className={style.sectionSearch}>
                            <h3>Ajouter un document</h3>
                            <input type="text" placeholder="Recherche..." onChange={e => this.setState({search: e.target.value})} value={this.state.search}/>
                        </div>
                        <ul className={style.list}>
                            {docs.map((item, key) => (
                                <li key={key}>
                                    <Row title={item.name} check={_.find(this.props.usedType, t => t.type === item.key)} unique={false} onAdd={e => this.clickDocument(item.key, item.location)}/>
                                </li>
                            ))}
                        </ul>
                    </div>
                </div>
                <Portal isOpened={this.state.open} closeOnEsc onClose={() => this.setState({open: false})}>
                    <Modal>
                        <Importer/>
                    </Modal>
                </Portal>
            </Page>
        )
    }
}

const Row = props => {

    const button = props.unique ? (props.check ? "Modifier" : "Ajouter") : (props.check ? "Ajouter un nouveau" : "Ajouter")

    return (
        <div className={classNames(style.row)}>
            <div className={classNames(style.check, {[style.checked]: props.check})}>
                {props.check && (<i className="ion-ios-checkmark-outline"></i>)}
            </div>
            <div className={style.name}>{props.title}</div>
            <div onClick={e => props.onAdd && props.onAdd(e)} className={classNames(style.button)}>{button}</div>
        </div>
    )
}


