import React, {Component} from 'react'
import classNames from 'classnames'
import style from '../../styles/ToolBoxOptions.sass'
import button from '../../styles/buttons.scss'

export default class ToolBoxOptions extends Component {
    constructor(props, context) {
        super(props, context)
        this.state = {
            open: false
        }

        this.intent = null

        this.handleOutsideMouseClick = this.handleOutsideMouseClick.bind(this)
    }

    componentWillUnmount() {
        clearTimeout(this.intent)        
    }

    componentDidMount() {
        document.addEventListener('click', this.handleOutsideMouseClick)
        document.addEventListener('touchstart', this.handleOutsideMouseClick)
    }

    componentWillUnmount() {
        document.removeEventListener('click', this.handleOutsideMouseClick)
        document.removeEventListener('touchstart', this.handleOutsideMouseClick)
    }

    handleOutsideMouseClick(e) {
        if(!this.state.open) return
            
        if (this.refs.root.contains(e.target) || (e.button && e.button !== 0)) {
            return
        }   

        this.close()
    }

    open() {
        if(this.state.open)
            return

        this.setState({
            open: true
        })
        this.props.onOpen && this.props.onOpen()
    }

    close() {
        if(!this.state.open)
            return

        this.setState({
            open: false
        })
        this.props.onClose && this.props.onClose()
    }

    toggle() {
        if(this.props.disabled) return

        if (this.state.open)
            this.close()
        else
            this.open()
    }

    closeIntent() {
        if(this.props.noAuto) return
        this.intent = setTimeout(() => this.close(), 100)
    }

    render() {
        return (
            <div ref="root" className={style.toolBox} onClick={e => e.stopPropagation()}>

                <button type="button" onClick={e => this.toggle()} className={classNames(button.button, button.stable)}>
                    <i className="ion-more"></i>
                </button>

                {this.state.open && (
                <div className={style.content} onClick={e => this.close()}>
                    <div className={style.triangle}></div>
                    {this.props.children}
                </div>)}
            </div>
        )
    }
}



