import React, {Component} from 'react'
import classNames from 'classnames'
import Portal from 'react-portal'
import {Link} from 'react-router-dom'
import {createFakePath, isDirectory} from '../../tools'
import style from '../../styles/Documents.sass'
import button from '../../styles/buttons.scss'

import Page from './Page'
import Loader from './Loader'
import DocumentList from './DocumentList'
import DataList from './DataList'
import DocumentView from './DocumentView'
import ToolBoxOptions from './ToolBoxOptions'
import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'
import Selector from '../../containers/Selector'
import RenameInput from '../utils/RenameInput'

import {
    STATUS_DONE,
    STATUS_LOAD,
    STATUS_ERROR,
    ROOT_PATH,
    VIEW_LIST,
    SORT_DATE,
    SORT_NAME,
    FOLDER_LIST
} from '../../constants'
const protectedFolders = FOLDER_LIST

import {getInfosFromPath} from '../../scripts/dataFields'
import {getDisplayName} from '../../scripts/fileTypes'

import confirm from '../../scripts/confirm'
import ConfirmModal from '../modals/ConfirmModal'
import PromptModal from '../modals/PromptModal'

const modal = (
    <ConfirmModal>
        <p>
            Voulez-vous supprimer ce(s) élément(s) ?
        </p>
    </ConfirmModal>
)

const inludeOne = (from, to) => {
    for (const el of from) {
        if (!el.path) 
            continue
        if (to.indexOf(el.path) !== -1) 
            return true
    }
    return false
}

export default class Explorer extends Component {

    constructor(props, context) {
        super(props, context)

        this.state = {
            importerOpen: false,
            moveModalOpen: false,
            doctype: false,
            renaming: false
        }
    }

    componentWillMount() {
        this.checkForData(this.props.path)
    }

    componentWillReceiveProps(newProps) {
        this.checkForData(newProps.path)
    }

    checkForData(newPath) {
        const info = getInfosFromPath(newPath)
        if (info) {
            if (!this.state.doctype) {
                this.state.doctype = info.doctype
                this.props.onLoadData(info.doctype)
            } else if (this.state.doctype !== info.doctype) {
                this.props.onLoadData(info.doctype)
            }
        } else {
            this.state.doctype = false
        }
    }

    openImporter() {
        this.props.onImporterClick(this.props.path)
        this.setState({importerOpen: true})
    }

    openMover() {
        this.props.onImporterClick(this.props.path)
        this.setState({moveModalOpen: true})
    }

    remove() {
        if (this.props.selectedCount + this.props.selectedDataCount <= 0) 
            return

        confirm(modal).then(() => {
            if (this.props.selectedCount > 0) 
                this.props.onRemove(this.props.files.filter(f => f.selected))

            if (this.props.selectedDataCount > 0) 
                this.props.onRemoveData(this.state.doctype, this.props.data.filter(f => f.selected))
        }).catch(err => {})

    }

    move(path) {
        if (this.props.folder.path === path) 
            return
        this
            .props
            .onMoveFile(this.props.files.filter(f => f.selected), path)
    }

    download() {
        this
            .props
            .onDownload(this.props.files.filter(f => f.selected))
    }

    rename(name) {
        this.props.onRename(this.state.renaming.id, name)
        this.setState({renaming: false})
    }

    createBreadcrums(disableLink = false) {
        const displayPath = this.props.location.pathname.substring(1);
        const foldersSplit = displayPath.split('/');
        const folders = [];

        if (foldersSplit.length > 1) {
            var root = foldersSplit[0];
            folders.push({
                path: root,
                fullPath: '/' + root
            });

            if (foldersSplit.length > 3) {
                var dot = foldersSplit[foldersSplit.length - 3];
                folders.push({
                    path: '/...',
                    fullPath: '/' + foldersSplit
                        .slice(0, foldersSplit.length - 2)
                        .join('/')
                });

                var f = foldersSplit[foldersSplit.length - 2];
                folders.push({
                    path: '/' + f,
                    fullPath: '/' + foldersSplit
                        .slice(0, foldersSplit.length - 1)
                        .join('/')
                });
            } else if (foldersSplit.length > 2) {
                var f = foldersSplit[foldersSplit.length - 2];
                folders.push({
                    path: '/' + f,
                    fullPath: '/' + foldersSplit
                        .slice(0, foldersSplit.length - 1)
                        .join('/')
                });
            }

        }
        var lastFolder = (foldersSplit.length > 1
            ? '/'
            : '') + foldersSplit[foldersSplit.length - 1];

        return (
            <span className={style.breadcrums}>
                {folders.map((el, key) => (
                    <Link key={key} to={el.fullPath} onClick={e => {
                        if(disableLink) e.preventDefault()
                    }}>
                        {el.path}
                    </Link>
                ))}
                <strong>{lastFolder}</strong>
            </span>
        )
    }

    renderDocuments() {
        if (this.props.files.length <= 0) return

        const {history, viewMode, files, folder, onSelect} = this.props

        return (
            <DocumentList
                view={viewMode}
                onClickItem={item => {
                    let path
                    if (isDirectory(item)) {
                        path = createFakePath(item.path)
                    } else { //check for simple file
                        path = createFakePath(folder.path + '/' + item.name)
                    }
                    history.push(path) 
                }}
                onSelectItem={item => onSelect(item)}>
                {files}
            </DocumentList>
        )
    }

    renderData() {
        if (!this.state.doctype) 
            return

        const {history, viewMode, data, onSelectData} = this.props

        const slug = getInfosFromPath(this.props.path).slug

        return (
            <DataList
                view={viewMode}
                onClickItem={item => {
                history.push('/data/' + slug + '/' + item._id)
            }}
                onSelectItem={item => onSelectData(item)}>
                {data}
            </DataList>
        )
    }

    renderError() {
        return (
            <div>
                <h3>Error</h3>
                <p>Essayer plus trad</p>
            </div>
        )
    }

    render() {
        const { files, folder, fileCount, status, selectedCount, dataStatus, dataCount, selectedDataCount } = this.props

        const loading = status === STATUS_LOAD || dataStatus === STATUS_LOAD

        let empty = !loading && ( ( fileCount === 0 ) && ( this.state.doctype && dataCount === 0 ) )

        let title = null
        if(this.state.renaming) {
            title = (<RenameInput name={this.state.renaming.name} onRename={name => this.rename(name)} onCancel={() => this.setState({renaming: false})}/>)
        } else {
            title = this.createBreadcrums(loading)
        }

        //Create actions
        const importer = (<button disabled={loading} onClick={() => this.openImporter()} className={classNames(button.button, button.default)} title="Importer"><i className="ion-ios-plus-empty"></i><span className={button.text}>AJOUTER</span></button>)
        const toolbox = (
            <ToolBoxOptions disabled={loading}>
                <ul>
                    <li onClick={() => {
                        this.props.onChangeViewMode()
                    }}>
                        {this.props.viewMode === VIEW_LIST ? (
                            <i className="ion-grid">VUE GRILLE</i>
                        ) : (
                            <i className="ion-navicon">VUE LISTE</i>
                        )}
                    </li>
                    <li onClick={() => {
                        confirm(<PromptModal/>)
                        .then(name => this.props.onNewFolder(name) )
                        .catch(err => {})
                    }}>
                        <i className="ion-ios-plus-empty">NOUVEAU DOSSIER</i>
                    </li>
                    <li onClick={() => {
                        this.props.onDownloadAll(this.props.files.slice(0))
                    }}>
                        <i className="ion-ios-cloud-download-outline">TOUT TELECHARGER</i>
                    </li>
                </ul>
            </ToolBoxOptions>
        )

        //optional buttons
        let removeDisabled = true
        let downloadDisabled = true
        let moveDisabled = true
        let renameDisabled = true

        const selectedFiles = this.props.files.filter(f => f.selected)
        let protectedInSelection = false
        if(selectedCount > 0 || selectedDataCount > 0) {
            protectedInSelection = inludeOne(selectedFiles, protectedFolders)
            if(!protectedInSelection) {
                removeDisabled = false
            }
        }
        if(selectedCount > 0) {
            downloadDisabled = false

            if(!protectedInSelection) {
                moveDisabled = false

                if(selectedCount === 1) {
                    renameDisabled = false
                }
            }
        }
        const remove = (<button disabled={removeDisabled} onClick={() => this.remove()} className={classNames(button.button, button.assertive, {[button.disabled]: removeDisabled})} title="Supprimer"><i className="ion-ios-trash-outline"></i><span className={button.text}>SUPPRIMER</span></button>)
        const download = (<button disabled={downloadDisabled} onClick={() => this.download()} className={classNames(button.button, button.stable, {[button.disabled]: downloadDisabled})} title="Télécharger"><i className="ion-ios-cloud-download-outline"></i><span className={button.text}>TELECHARGER</span></button>)
        const move = (<button disabled={moveDisabled} onClick={() => this.openMover()} className={classNames(button.button, button.stable, {[button.disabled]: moveDisabled})} title="Déplacer"><i className="ion-ios-browsers-outline"></i><span className={button.text}>DEPLACER</span></button>)
        const rename = (<button disabled={renameDisabled} className={classNames(button.button, button.stable, {[button.disabled]: renameDisabled})} onClick={() => this.setState({renaming: selectedFiles.pop()})} title="Renommer"><i className="ion-ios-compose-outline"></i><span className={button.text}>RENOMMER</span></button>)

        const filter = (
            <select defaultValue={this.props.filter} onChange={e => this.props.onChangeFilter(e.target.value)}>
                <option value={SORT_DATE}>TRIER PAR DATE</option>
                <option value={SORT_NAME}>TRIER PAR NOM</option>
            </select>
        )

        const actions = [rename, move, download, remove, importer, toolbox]

        let count = fileCount
        if(this.state.doctype) count += dataCount

        let documents, data, fileViewer, error
        if(!loading) {
            // no error
            if(this.props.status !== STATUS_ERROR && this.props.dataStatus !== STATUS_ERROR) {
                documents = this.renderDocuments()
                data = this.renderData()
            } else {
                error = this.renderError()
            }
        }

        let subtitle = count + " élément" + (count > 1 ? "s" : "")

        return (
            <Page title={title} 
                subtitle={!loading && subtitle} 
                actions={actions}
                onBackPress={this.props.match.params.path && !loading && (() => this.props.history.goBack())}
                options={[filter]}>

                <Loader display={loading}/>

                {empty && "Dossier vide"}

                {data}
                {documents}
                {error}

                <Portal closeOnEsc isOpened={this.state.importerOpen} onClose={() => this.setState({importerOpen: false})}>
                    <Modal>
                        <Importer/>
                    </Modal>
                </Portal>

                <Portal closeOnEsc isOpened={this.state.moveModalOpen} onClose={() => this.setState({moveModalOpen: false})}>
                    <Modal>
                        <Selector onSelect={path => this.move(path)} hideFiles={true}/>
                    </Modal>
                </Portal>

            </Page>
        )

    }
}


