import moment from 'moment'

const doctype = "io.cozy.jobs"

const jobsQueueUrl = `/jobs/queue/sendmail`
const triggersUrl = `/jobs/triggers`


const fetchJSON = async(path, method = 'GET', body = {}) => {
    const credentials = await cozy.client.authorize()
    const url = await cozy.client.fullpath(path)

    let headers = {}
    headers['Content-Type'] = 'application/json'
    headers['Accept'] = 'application/json'
    headers['Authorization'] = credentials.token.toAuthHeader()

    var options = {
        headers,
        method,
        credentials: 'include'
    }

    if (method === 'POST') {
        body.data.type = doctype
        options.body = JSON.stringify(body)
    }
    return fetch(url, options)

}

export const deleteTrigger = id => {
    return fetchJSON(triggersUrl + '/' + id, 'DELETE')
        .then(response => {
            return response.text()
        })
}

export const send = (options) => {
    if(options === undefined) return Promise.reject("No arguments")

    let date = moment(options.date, 'DD/MM/YYYY').toDate()

    let mail = formatJobs(options.deadline, options.recallName, options.username)
    return fetchJSON(jobsQueueUrl, 'POST', mail)
        .then(response => {
            return response.json()
        })
}

export const trigger = (options) => {
    if(options === undefined) return Promise.reject("No arguments")

    let date = moment(options.date, 'DD/MM/YYYY').toDate()

    let mail = formatTrigger(date, options.deadline, options.recallName, options.username)
    return fetchJSON(triggersUrl, 'POST', mail)
        .then(response => {
            return response.json()
        })
}

export const getTriggers = (id) => {
    if(id) id = '/'+id
    else id = ''
    
    return fetchJSON(triggersUrl + id)
        .then(response => {
            return response.json()
        })
}

export const getJobs = () => {
    return fetchJSON(jobsQueueUrl)
        .then(response => {
            return response
        })
}

const formatTrigger = (date, deadline, recallName, username) => {
    if (!date || !(date instanceof Date)) throw Error("Bad date argument")

    return {
        "data": {
            "attributes": {
                "type": "@at",
                "arguments": date.toJSON(),
                "worker": "sendmail",
                "worker_arguments": formatMail(deadline, recallName, username),
                "options": {}
            }
        }
    }
}

const formatJobs = (deadline, recallName, username) => {
    return {
        "data": {
            "attributes": {
                "options": {},
                "arguments": formatMail(deadline, recallName, username)
            }
        }
    }
}

const formatMail = (deadline, recallName, username) => {
    return {
        mode: "noreply",
        subject: "Rappel Homebook",
        parts: [{
            type: "text/plain",
            body: formatText(deadline, recallName, username)
        }]
    }
}

const formatText = (deadline, recallName, username = '') => {
    // let displayDeadline = moment(deadline).format('DD/MM/YYYY')

    return `Bonjour ${username},

Homebook vous previent que le rappel ${recallName} a pour échéance le ${deadline}.

Cordialement,
Homebook`
}


