import fileTypes from '../constants/types.json'

import { ROOT_PATH } from '../constants'

export const getFileTypes = () => {

    var ret = []

    for(let elem of fileTypes) {
        for(let type of elem.types) {
            type.location = ROOT_PATH + elem.location
            ret.push(type)
        }
    }

    const types = ret
    return types
}

export const getTypesByGroup = () => {

    return fileTypes

}

export const getDisplayName = (slug) => {

    for(let elem of fileTypes) {
        for(let type of elem.types) {
            if(type.key === slug) {
                return type.name
            }
        }
    }

}



