import React, { Component } from 'react'
import _ from 'lodash'
import moment from 'moment'
import { Link } from 'react-router-dom'
import classNames from 'classnames'

import Page from './Page'
import Etiquette, {NRJ, CO2} from '../utils/EtiquetteEnergy'

import style from '../../styles/Dashboard.sass'

import { getDataElements } from '../../tools'
import { getInfosFromDoctype } from '../../scripts/dataFields'
import { DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS, DOCTYPE_NEWS, DOCTYPE_RECALLS } from '../../constants'

const doctypeSlug = {
    [DOCTYPE_DIAGNOSIS] : getInfosFromDoctype(DOCTYPE_DIAGNOSIS),
    [DOCTYPE_HOMEDATA] : getInfosFromDoctype(DOCTYPE_HOMEDATA),
    [DOCTYPE_EQUIPMENTS] : getInfosFromDoctype(DOCTYPE_EQUIPMENTS),
    [DOCTYPE_MAINTENANCES] : getInfosFromDoctype(DOCTYPE_MAINTENANCES),
    [DOCTYPE_WORKS] : getInfosFromDoctype(DOCTYPE_WORKS),
}

export default class Dashboard extends Component {

    constructor(props) {
        super(props)

        this.state = {
            recalls: [],
            homedata: {},
            news: [],
            events: [],
            NRJconsommation: undefined,
            CO2emission: undefined,
            timelineFilterSlug: '' 
        }

        this.renderTimelineFilter = this.renderTimelineFilter.bind(this)
        this.filterTimeline = this.filterTimeline.bind(this)

        this.unmounted = false
    }

    componentWillMount() {
        this.load() //load all necessary data : Recall, News, Homedata
    }

    componentWillUnmount() {
        this.unmounted = true
    }

    componentWillReceiveProps(newProps) {
    }

    load() {
        getDataElements(DOCTYPE_NEWS)
        .then(news => {
            if(!this.unmounted) this.setState({news})
        })
        getDataElements(DOCTYPE_HOMEDATA)
        .then(all => {
            const homedata = _.find(all, item => !item.trash)
            if(homedata) {
                if(!this.unmounted) {
                    const newState = {	
                        homedata,
                    }
                    if(homedata.constructionDate && homedata.constructionDate !== "Invalid date") {
                        newState.events= [...this.state.events, {name: 'Construction du logement', date: homedata.constructionDate, slug: doctypeSlug[DOCTYPE_HOMEDATA].slug, id:homedata._id, doctypeName: doctypeSlug[DOCTYPE_HOMEDATA].name}]
                    }
                    if(homedata.buyDate && homedata.buyDate !== "Invalid date") {
                        newState.events= [...this.state.events, {name: 'Achat du logement', date: homedata.buyDate, slug: doctypeSlug[DOCTYPE_HOMEDATA].slug, id:homedata._id, doctypeName: doctypeSlug[DOCTYPE_HOMEDATA].name}]
                    }
                    this.setState(newState)
                }
                    
            }
        })
        getDataElements(DOCTYPE_RECALLS)
        .then(recalls => {
            if(!this.unmounted) this.setState({recalls})
        })
        getDataElements(DOCTYPE_DIAGNOSIS)
        .then(dias => {
            let lastDPE, lastDPEDate
            const newEvents = []
            for(const item of dias){
                if(item.trash) continue

                if(item.type === 'DPE') {
                    const date = moment(item.date, 'DD/MM/YYYY').unix()
                    if(!lastDPEDate ||lastDPEDate < date){
                        lastDPEDate = date
                        lastDPE = item
                    }
                }

                const date = item.date
                if(date) newEvents.push({date, name: item.name || item.type, slug: doctypeSlug[DOCTYPE_DIAGNOSIS].slug, id: item._id, doctypeName: doctypeSlug[DOCTYPE_DIAGNOSIS].name})
            }
            const newState = {events: [...this.state.events, ...newEvents]}
            if(lastDPE) {
                newState.CO2emission = lastDPE.CO2emission
                newState.NRJconsommation = lastDPE.realEnergyConsommation
            }
            if(!this.unmounted) this.setState(newState)
        })

        const all = [
            getDataElements(DOCTYPE_EQUIPMENTS, true),
            getDataElements(DOCTYPE_WORKS, true),
            getDataElements(DOCTYPE_MAINTENANCES, true)
        ]
        Promise.all(all)
        .then(list => {
            const events = []
            for(const el of list) {
                for(const item of el){
                    if(item.trash) continue

                    const date = item.date || item.dateStart || item.buyDate
                    if(date) events.push({date, name: item.name || item.type, slug: doctypeSlug[item.doctype].slug, id: item._id, doctypeName: doctypeSlug[item.doctype].name})
                }
            }
            if(!this.unmounted) this.setState({events: [...this.state.events, ...events]})
        })
    }

    renderTimelineFilter() {
        const keys = Object.keys(doctypeSlug)
        return (
            <select value={this.state.timelineFilterSlug} onChange={e => this.setState({timelineFilterSlug: e.target.value})}>
                <option key={-1} value="">Tout</option>
                {keys.map((item, key) => (
                    <option key={key} value={doctypeSlug[item].slug}>{doctypeSlug[item].name}</option>
                ))}
            </select>
        )
    }

    filterTimeline(el) {
        if(this.state.timelineFilterSlug === '') return true
        return el.slug === this.state.timelineFilterSlug
    }

    render() {

        const events = this.state.events && this.state.events.filter(this.filterTimeline)

        return (
            <Page title="Accueil">
                
                <div className={style.main}>
                    <div className={style.row}>
                        <div className={classNames(style.home, "panel", "panel-default")}>
                            <div className="panel-heading">Fiche d’identité de mon logement</div>
                            <div className="panel-body">
                                {this.state.homedata && (
                                    <div className={style.infos}>
                                        <p>Type de logement : {this.state.homedata.type || 'Donnée non renseignée'}</p>
                                        <p>Votre statut : {this.state.homedata.status || 'Donnée non renseignée'}</p>
                                        <p>Adresse : {_.map(this.state.homedata.address, (item, key) => (
                                            <span key={key}>{item}, </span>
                                        ))}</p>
                                        <p>Date de construction : {this.state.homedata.constructionDate || 'Donnée non renseignée'}</p>
                                        <p>Nombre de pièces : {this.state.homedata.roomsCount || 'Donnée non renseignée'}</p>
                                        <p>Surface habitable : {this.state.homedata.livingArea || 'Donnée non renseignée'}</p>
                                    </div>
                                )}
                                <div className={style.etiquettes}>
                                    <Etiquette type={NRJ} value={this.state.NRJconsommation} size={320}/>
                                    <Etiquette type={CO2} value={this.state.CO2emission} size={320}/>
                                </div>
                            </div>
                        </div>
                        <div className={style.col}>
                            <div className={classNames(style.actus, "panel", "panel-default")}>
                                <div className="panel-heading">Fil d'actualités</div>
                                <div className="panel-body">
                                    {this.state.news && this.state.news.length > 0 && this.state.news.slice(0, 5).map((item, key) => (
                                        <Link to={'/actualites'} key={key} style={{display: 'block'}}>
                                            <strong>{item.title}</strong> - <span>{moment(item.updatedAt).format('DD/MM/YYYY')}</span>
                                        </Link>
                                    ))}
                                    {this.state.news && this.state.news.length === 0 && (
                                        <Link to={'/actualites'} style={{display: 'block'}}>
                                            <span>Aucune actualité</span>
                                        </Link>
                                    )}
                                </div>
                            </div>
                            <div className={classNames(style.recalls, "panel", "panel-default")}>
                                <div className="panel-heading">Prochains rappels</div>
                                <div className="panel-body">
                                    {this.state.recalls && this.state.recalls.length > 0 && this.state.recalls.slice(0, 5).map((item, key) => (
                                        <Link to={'/rappels'} key={key} style={{display: 'block'}}>
                                            <strong>{item.name}</strong> - <span>{item.deadline}</span>
                                        </Link>
                                    ))}
                                    {this.state.recalls && this.state.recalls.length === 0 && (
                                        <Link to={'/rappels'} style={{display: 'block'}}>
                                            <span>Aucun rappels</span>
                                        </Link>                                        
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className={classNames(style.timeline, "panel", "panel-default")}>
                        <div className={classNames("panel-heading", style.panelTitle)}><span>Historique</span><span>Filtre: {this.renderTimelineFilter()}</span></div>
                        <div className="panel-body">
                            {this.state.events && events.length > 0 && events.filter(this.filterTimeline).sort(sortDateMoment).map((item, key) => (
                                <div className={style.timeRow} key={key}>
                                    <div className={style.date}>{item.date}</div>
                                    <Link to={`/data/${item.slug}/${item.id}`} style={{display: 'block'}}>{`${item.doctypeName}: ${item.name}`}</Link>
                                </div>
                            ))}
                            {this.state.events && events.length === 0 && (
                                <Link to={'/assistant'} style={{display: 'block'}}>
                                    <span>Aucun événements</span>
                                </Link>                                
                            )}
                        </div>
                    </div>
                </div>

            </Page>
        )
    }
}

const sortDateMoment = (a, b) => {
    const aa = moment(a.date, 'DD/MM/YYYY')
    const bb = moment(b.date, 'DD/MM/YYYY')

    if(aa.isAfter(bb)) return 1
    if(aa.isBefore(bb)) return -1
    return 0
}



