import { combineReducers } from 'redux'

import {
    CONTACTS_NEW,
    CONTACTS_NEW_SUCCESS,
    CONTACTS_NEW_FAILURE,
    CONTACTS_REMOVE,
    CONTACTS_REMOVE_SUCCESS,
    CONTACTS_REMOVE_FAILURE,
    CONTACTS_SAVE,
    CONTACTS_SAVE_SUCCESS,
    CONTACTS_SAVE_FAILURE,
    CONTACTS_SELECT,
    LOAD_CONTACTS,
    LOAD_CONTACTS_SUCCESS,
    LOAD_CONTACTS_FAILURE,
    CONTACTS_RESTORE_SUCCESS
} from '../actions'

import { STATUS_DONE, STATUS_ERROR, STATUS_LOAD } from '../constants'

export const elements = (state=[], action) => {
    switch (action.type) {
        case LOAD_CONTACTS_SUCCESS:
            return action.contacts
        case CONTACTS_NEW_SUCCESS:
            return [...state, action.contact]
        case CONTACTS_REMOVE_SUCCESS:
            return state.filter(f => action.ids.indexOf(f._id) === -1)
        case CONTACTS_SAVE_SUCCESS:
            const withoutContact = state.filter(f => f._id !== action.contact._id)
            return [...withoutContact, action.contact]
        case CONTACTS_SELECT:
            const updated = state.map(f => {
                if(f._id === action.contact._id) f.selected = !f.selected
                return f
            })
            return [...updated]
        default:
            return state
    }
}

export const selectedCount = (state=0, action) => {
    switch (action.type) {
        case LOAD_CONTACTS_SUCCESS:
        case CONTACTS_REMOVE_SUCCESS:
            return 0
        case CONTACTS_SELECT:
           if(action.contact.selected)
                return state - 1
            else
                return state + 1
        default:
            return state
    }
}

const elementCount = (state = null, action) => {
    switch (action.type) {
        case LOAD_CONTACTS_SUCCESS:
            return action.contacts.length
        case CONTACTS_REMOVE_SUCCESS:
            return state - action.ids.length
        case CONTACTS_RESTORE_SUCCESS:
            return state + action.ids.length
        case CONTACTS_NEW_SUCCESS:
            return state + 1
        default:
            return state
    }
}

const fetchStatus = (state = STATUS_LOAD, action) => {
    switch (action.type) {
        case LOAD_CONTACTS: 
        case CONTACTS_REMOVE:
        case CONTACTS_SAVE:
        case CONTACTS_NEW:
            return STATUS_LOAD
        case LOAD_CONTACTS_SUCCESS:
        case CONTACTS_NEW_SUCCESS:
        case CONTACTS_REMOVE_SUCCESS:
        case CONTACTS_SAVE_SUCCESS:
            return STATUS_DONE
        case LOAD_CONTACTS_FAILURE:
            return STATUS_ERROR
        default:
            return state
    }
}

export default combineReducers({
    selectedCount,
    elements,
    elementCount,
    fetchStatus
})


