import {
    IMPORTER_CLICK,
    IMPORTER_DIRECTORY_CHANGE,
    IMPORTER_FILE_UPLOAD,
    IMPORTER_FILE_UPLOAD_FAILURE,
    IMPORTER_FILE_UPLOAD_SUCESS,
    IMPORTER_VIEW_CHANGE,
    IMPORTER_TREE_SUCCESS,
} from '../actions'

import { VIEW_DIR, VIEW_FILE, STATUS_DONE, STATUS_ERROR, STATUS_LOAD, ROOT_PATH } from '../constants'

const defaultState = {
    currentPath: "",
    currentType: "",
    documentsTree: {},
    view: VIEW_FILE,
    status: STATUS_DONE
}

export const importer = (state=defaultState, action) => {
    switch (action.type) {
        case IMPORTER_DIRECTORY_CHANGE:
            return {...state, currentPath: action.path}
        //Shortcut for IMPORTER_DIRECTORY_CHANGE
        case IMPORTER_CLICK:
            return {...state, currentPath: action.path, currentType: action.slug, view: VIEW_FILE}
        case IMPORTER_VIEW_CHANGE:
            return {...state, view: action.view}
        case IMPORTER_TREE_SUCCESS: 
            return {...state, documentsTree: action.tree}
        case IMPORTER_FILE_UPLOAD:
            return {...state, status: STATUS_LOAD}
        case IMPORTER_FILE_UPLOAD_SUCESS:
            return {...state, status: STATUS_DONE}
        case IMPORTER_FILE_UPLOAD_FAILURE:
            return {...state, status: STATUS_ERROR}
        default:
            return state
    }
}

export default importer


