import React, {Component} from 'react'
import classNames from 'classnames'
import {Link} from 'react-router-dom'
import Portal from 'react-portal'

import button from '../../styles/buttons.scss'
import style from '../../styles/Documents.sass'

import Loader from './Loader'
import Modal from '../modals/Modal'
import Page from './Page'
import DataTable from './DataTable'
import DocumentList from './DocumentList'
import Selector from '../../containers/Selector'

import { getInfosFromSlug, getFields } from '../../scripts/dataFields'

import { STATUS_LOAD, ROOT_NAME, VIEW_LIST } from '../../constants'
import { createFakePath, isDirectory, extractFileAttributes } from '../../tools'

export default class Data extends Component {

    constructor(props, context) {
        super(props, context)

        this.state = {
            edition: false,
            referenced: false,
            modalOpen: false,
        }

        this.renderData = this.renderData.bind(this)
    }

    componentWillMount () {
        this.infos = getInfosFromSlug(this.props.match.params.slug)
        this.id = this.props.match.params.id

        this.props.onLoadData(this.infos.doctype, this.id)
    }

    componentWillReceiveProps(newProps) {
        if(this.props.current != newProps.current) {
            if(newProps.current._id) {
                cozy.client.data.listReferencedFiles(newProps.current)
                .then(list => Promise.all(list.map(id => cozy.client.files.statById(id))))
                .then(list => {
                    const files = list.filter(f => !f.attributes.trashed).map(extractFileAttributes)
                    this.setState({referenced: files})
                })
            }
        }
    }

    openModal() {
        this.props.onImporterClick(this.infos.path)
        this.setState({modalOpen: true})
    }

    addRef(path) {
        cozy.client.files.statByPath(path)
        .then(file => {
            return cozy.client.data.addReferencedFiles(this.props.current, file._id)
        })
        .then(() => this.props.onLoadData(this.infos.doctype, this.id))
    }

    save() {
        this.setState({
            edition: false
        })

        this.props.dataSave(this.infos.doctype, this.props.current)
    }

    edit() {
        this.setState({
            edition: true
        })
    }

    remove() {
        this.props.dataRemove(this.infos.doctype, [this.props.current])
        .then(e => this.props.history.goBack())
    }

    addMulti(type, group) {
        if(!this.state.edition) return
        const { current } = this.props

        const tmp = group.map(g => ({key: g.name, value: (g.values && g.values[0]) || ''}))
        let def = {}
        for(let e of tmp) {
            def[e.key] = e.value
        }
        if(!current[type]) current[type] = [def]
        else current[type].push(def)

        console.log(this.props.current);
        this.forceUpdate()
    }

    renderData() {
        if(!this.props.current) return

        const fields = getFields(this.infos.doctype)
        if(!fields) return

        const { current } = this.props

        return (
            <DataTable>
                {fields.map((group, keygroup) => {
                    if(group.multi) {
                        const multi = current[group.type]
                        if(!multi || multi.length <= 0) {
                            return (
                                <DataTable.Group title={group.name} key={keygroup} displayAdd={this.state.edition} onAdd={e => this.addMulti(group.type, group.group)}>
                                    <p>Aucun élément</p>
                                </DataTable.Group>
                            )
                        } else {
                            return multi.map((elem, keyelem) => (
                                <DataTable.Group title={`${group.name} ${keyelem+1}`} key={keygroup * 1000 + keyelem} displayAdd={this.state.edition} onAdd={e => this.addMulti(group.type, group.group)}>
                                    {group.group.map((item, key) => {
                                        return (
                                            <DataTable.Item
                                                key={key}
                                                title={item.display}
                                                value={elem[item.name]}
                                                type={item.type}
                                                updatable={this.state.edition}
                                                values={item.values}
                                                onChange={e => elem[item.name] = e}/>
                                        )
                                    })}
                                </DataTable.Group>
                            ))
                        }
                    } else {
                        return (
                            <DataTable.Group title={group.name} key={keygroup}>
                                {group.group.map((item, key) => {
                                    return (<DataTable.Item
                                        key={key}
                                        title={item.display}
                                        value={current[item.name]}
                                        type={item.type}
                                        updatable={this.state.edition}
                                        values={item.values}
                                        onChange={e => current[item.name] = e}/>);
                                })}
                            </DataTable.Group>
                        )
                    }
                })}
            </DataTable>
        )
    }

    render() {
        const loading = this.props.status === STATUS_LOAD

        const actions = []

        const remove = (<button onClick={() => this.remove()} className={classNames(button.button, button.assertive)} title="Supprimer"><i className="ion-ios-trash-outline"></i><span className={button.text}>SUPPRIMER</span></button>)
        actions.push(remove)

        if(this.state.edition) {
            const save = (<button onClick={() => this.save()} className={classNames(button.button, button.stable)} title="Sauvegarder"><i className="fa fa-floppy-o"></i><span className={button.text}>SAUVEGARDER</span></button>)
            actions.unshift(save)
        } else {
            const edit = (<button onClick={() => this.edit()} className={classNames(button.button, button.stable)} title="Modifier"><i className="fa fa-edit"></i><span className={button.text}>MODIFIER</span></button>)
            actions.unshift(edit)            	
        }

        const title = (
            <span className={style.breadcrums}>
                <Link to={createFakePath(this.infos.path)}>{this.infos.name}</Link>
                {this.props.current && this.props.current.name && (
                    <strong>
                        /{this.props.current.name}
                    </strong>
                )}
            </span>
        )

        return (
            <Page title={title} subtitle={''} 
                actions={actions}
                onBackPress={() => this.props.history.goBack()}>

                <Loader display={loading}/>

                {!loading && this.renderData()}
                {!loading && this.state.referenced && (
                    <div className="referencedFiles">
                        <div className={style.row}>
                            <h3>Documents liés</h3>
                    	    <button onClick={() => this.openModal()} className={classNames(button.button, button.stable)} title="Ajouter">
                                <i className="ion-ios-plus-empty"></i>AJOUTER
                            </button>
                        </div>
                        <DocumentList
                            view={VIEW_LIST}
                            onClickItem={item => {
                                if (isDirectory(item)) {
                                    const path = createFakePath(item.path)
                                    this.props.history.push(path) 
                                } else { //check for simple file
                                    cozy.client.files.statById(item.dir_id)
                                    .then(folder => {
                                        const path = createFakePath(folder.attributes.path + '/' + item.name)
                                        this.props.history.push(path) 
                                    })
                                }
                            }}>
                            {this.state.referenced}
                        </DocumentList>
                    </div>
                )}
                <Portal closeOnEsc isOpened={this.state.modalOpen} onClose={() => this.setState({modalOpen: false})}>
                    <Modal>
                        <Selector files onSelect={path => this.addRef(path)}/>
                    </Modal>
                </Portal>
            </Page>
        )
    }
}


