import React, {Component} from 'react'
import style from '../../styles/Navigation.sass'
import { withRouter, Link } from 'react-router-dom'
import classNames from 'classnames'

export const Navigation = (props) => {
    return (
        <nav className={style.main}>
           <div className={style.nav}>
                <ul className={style.list}>
                    <li className={style.button}>
                        <ActiveLink to="/accueil" activeClassName={style.active} title='Accueil'>
                            <i className="ion-ios-speedometer-outline"></i>Accueil
                        </ActiveLink>
                    </li>  
                    <li className={style.button}>
                        <ActiveLink to="/Documents" activeClassName={style.active} title='Mon logement'>
                            <i className="ion-ios-home"></i>Mon logement
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/contacts" activeClassName={style.active} title='Contacts'>
                            <i className="ion-ios-bookmarks-outline"></i>Contacts
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/rappels" activeClassName={style.active} title='Rappels'>
                            <i className="ion-ios-checkmark-outline"></i>Rappels
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/corbeille" activeClassName={style.active} title='Corbeille'>
                            <i className="ion-ios-trash-outline"></i>Corbeille
                        </ActiveLink>
                    </li>
                     <li className={style.button}>
                        <ActiveLink to="/assistant" activeClassName={style.active} title='Remplir son carnet'>
                            <i className="ion-ios-information-outline"></i>Remplir son carnet
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/du-nouveau" activeClassName={style.active} title='Du nouveau dans mon logement'>
                            <i className="ion-ios-chatboxes-outline"></i>Du nouveau dans mon logement
                        </ActiveLink>
                    </li>  
                </ul>
           </div>
           <div className={style.other}>
                <ul className={style.list}>
                    <li className={style.button}>
                        <ActiveLink to="/actualites" activeClassName={style.active} title='Actualités'>
                            Actualités
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/preferences" activeClassName={style.active} title='Préférences'>
                            Préférences
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/aide" activeClassName={style.active} title='Aide'>
                            Aide
                        </ActiveLink>
                    </li>
                    <li className={style.button}>
                        <ActiveLink to="/a-propos" activeClassName={style.active} title='À propos'>
                            À propos
                        </ActiveLink>
                    </li>
                </ul>
           </div>
        </nav>
    );
}

export const CustomLink = (props) => {
    const open = (e) => {
        e.preventDefault()
        if(props.onClick) props.onClick()
        props.history.push(props.to)
    }

    const { router, location, to, activeClassName, children, title } = props

    const active = location.pathname.indexOf(to) === 0

    return (
        <a className={active && activeClassName} onClick={e => open(e)} title={title}>
            {children}
        </a>
    )
}

const ActiveLink = withRouter(CustomLink)

export default Navigation


