import xmlbuiler from 'xmlbuilder'
import { parseString } from 'xml2js'
import _ from 'lodash'

import * as TempFile from './TempFile'
import {waitForJob} from './AutoImport'

import { VERSION, DOCTYPE_CONTACTS, DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS, COZY_ROOT_ID, ROOT_PATH } from '../constants'

import { getDataElements } from '../tools'

export const importData = async(file) => {

    //2. Upload the zip
    const zip = await TempFile.create(file, file.name)

    //3. Create directory in temp to receive unziped files
    const dir = await TempFile.createFolder("unzip")

    //4. Create the jobs to unzip the file in the temp folder
    const job = await cozy.client.jobs.create('unzip', {
        "zip": zip._id,
        "destination": dir._id
    })

    //5. Wait for the end of the jobs
    try {
        await waitForJob(job._id)
    } catch (error) {
        return console.error('Error in wait for job', error)
    }
    console.log('Unzip ok')

    //6. Get file content
    const newDir = await cozy.client.files.statById(dir._id)
    const dirContent = newDir.relations('contents')

    if(dirContent.lenght < 1) throw Error('No file unziped')

    const homebookDir = dirContent.shift() //Take the first child
    if(homebookDir.attributes.name !== "homebook") return console.error('Error: the zip in not a homebook exported file')

    const unzipedDir = await cozy.client.files.statById(homebookDir._id)
    const filesContent = unzipedDir.relations('contents') //all the content of the zip
    console.log('Files content 2', filesContent)

    for(let f of filesContent){

        if(f.attributes.name === 'homebook.xml') {
            //Parse homebook data
            try {
                //extract data from xml
                const link = await cozy.client.files.getDownloadLinkById(f._id)
                const fullLink = await cozy.client.fullpath(link)
                const res = await fetch(fullLink)
                const data = await res.text()
                const xml = await readXml(data)

                console.log('Data', xml);

                //create cozy data
                for(let el of xml.contacts) {
                    await cozy.client.data.create(DOCTYPE_CONTACTS, el)
                }
                await cozy.client.data.create(DOCTYPE_HOMEDATA, xml.homeData)
                for(let el of xml.equipments) {
                    await cozy.client.data.create(DOCTYPE_EQUIPMENTS, el)
                }
                for(let el of xml.diagnoses) {
                    await cozy.client.data.create(DOCTYPE_DIAGNOSIS, el)
                }
                for(let el of xml.maintenances) {
                    await cozy.client.data.create(DOCTYPE_MAINTENANCES, el)
                }
                for(let el of xml.works) {
                    await cozy.client.data.create(DOCTYPE_WORKS, el)
                }

            } catch (error) {
                console.error(error)
            }

        } else {
            //Move file or folder into homebook
            const homebookRoot = await cozy.client.files.statByPath(ROOT_PATH)
            await moveFile(f, homebookRoot)
        }
    }
    console.log('Importer END')
}

export const exportData = async() => {

    //Create XML and convert it to string
    const xml = await createXML()
    const xmlString = xml.end({
        pretty: true,
        indent: '  ',
        newline: '\n',
        allowEmpty: true
    })
    // console.log('XML string created', xmlString)

    //Create blob object
    const file = new Blob([xmlString], {type: "text/plain;charset=utf-8"})
    // console.log('XML data created', file);

    //upload into cozy at /tmp/homebook.xml
    await TempFile.deleteIfExist('homebook.xml')

    const xmlFile = await TempFile.create(file, 'homebook.xml')
    // console.log('XML file create and uploaded', xmlFile);

    //create download archive link
    const link = await cozy.client.files.getArchiveLinkByPaths([ROOT_PATH + '/', xmlFile.path], 'homebook')
    const reellink = await cozy.client.fullpath(link)
    // console.log('Download archive link generated', reellink);

    // Can't delete the temp file because make archive file empty

    return reellink
}

const createXML = async() => {
    //Create xml document
    const xml = xmlbuiler.create('homebook')
    .dec('1.0','UTF-8')
    // .dtd('homebook.dtd')
    .ins('xml-stylesheet', 'type="text/xsl" href="homebook.xsl"')
    
    //add version
    xml.ele('version', VERSION)

    //Get Contacts
    const contacts = await getDataElements(DOCTYPE_CONTACTS)
    const xmlContacts = xml.ele('contacts')

    for(const contact of contacts) {
        const xmlContact = createContact(contact)

        xmlContacts.importDocument(xmlContact)
    }

    //Get Homedata
    const homedataArray = await getDataElements(DOCTYPE_HOMEDATA)
    const homedata = homedataArray[0]

    if(homedata === undefined) {
        xml.ele('homedata')
    } else {
        const xmlHome = createHomedata(homedata)
        xml.importDocument(xmlHome)
    }

    //Get equipments
    const equipments = await getDataElements(DOCTYPE_EQUIPMENTS)
    const xmlEquipments = xml.ele('equipments')

    for(const equipment of equipments) {
        const xmlContact = createEquipment(equipment)

        xmlEquipments.importDocument(xmlContact)
    }

    //Get Works
    const works = await getDataElements(DOCTYPE_WORKS)
    const xmlWorks = xml.ele('works')

    for(const work of works) {
        const xmlWork = createWork(work)

        xmlWorks.importDocument(xmlWork)
    }

    //Get Maintenances
    const maintenances = await getDataElements(DOCTYPE_MAINTENANCES)
    const xmlMaintenances = xml.ele('maintenances')

    for(const maintenance of maintenances) {
        const xmlMaintenance = createMaintenance(maintenance)

        xmlMaintenances.importDocument(xmlMaintenance)
    }

    //Get Diagnoses
    const diagnoses = await getDataElements(DOCTYPE_DIAGNOSIS)
    const xmlDiagnoses = xml.ele('diagnoses')

    for(const diagnosis of diagnoses) {
        const xmlDiagnosis = createDiagnosis(diagnosis)

        xmlDiagnoses.importDocument(xmlDiagnosis)
    }

    return xml
}

const create = object => xmlbuiler.create(object)

const createDiagnosis = d => {
    //filtering attributes
    const element = {
        diagnosis: {
            type: d.type,
            description: d.description,
            date: d.date,
            expirationDate: d.expirationDate,
            DPEEnergyClass: d.DPEEnergyClass,
            realEnergyConsommation: d.realEnergyConsommation,
            carbonClass: d.carbonClass,
            CO2emission: d.CO2emission,
            cep: d.cep,
            bbio: d.bbio,
            tic: d.tic,
            data: d.data,
            equipmentData: d.equipmentData,
        }
    }

    return create(element)
}

const createMaintenance = d => {
    //filtering attributes
    const element = {
        maintenance: {
            name: d.name,
            description: d.description,
            contractId: d.contractId,
            date: d.date,
            deadline: d.deadline,
            contract: d.conract,
        }
    }

    return create(element)
}

const createWork = d => {
    //filtering attributes
    const element = {
        work: {
            type: d.type,
            description: d.description,
            roomNames: {
                roomName: d.roomNames
            },
            equipmentNames: {
                equipmentName: d.equipmentNames
            },
            craftsman: d.craftsman,
            cost: d.cost,
            dateStart: d.dateStart,
            dateEnd: d.dateEnd,
            contract: d.conract,
            satisfaction: d.satisfaction,
            status: d.status,
            commentary: d.commentary,
        }
    }

    return create(element)
}

const createEquipment = d => {
    //filtering attributes
    const element = {
        equipment: {
            type: d.type,
            buyDate: d.buyDate,
            model: d.model,
            brand: d.brand,
            installDate: d.installDate,
            guaranteeEndDate: d.guaranteeEndDate,
            lastMaintenanceDate: d.lastMaintenanceDate,
            energyClass: d.energyClass,
        }
    }

    return create(element)
}

const createHomedata = d => {
    //filtering attributes
    const element = {
        homeData: {
            type: d.type,
            status: d.status,
            constructionDate: d.constructionDate,
            address: d.address,
            email: d.email,
            phone: d.telephones && d.telephones.map(p => ({'@type': p.type || 'default', '#text': p.phone})),
            livingArea: d.livingArea,
            roomsCount: d.roomsCount,
            guarantees: {
                guarantee: d.guarantees
            },
            rooms: {
                room: d.rooms
            },
            contract: d.contract
        }
    }

    return create(element)
}

const createContact = d => {
    //filtering attributes
    const element = {
        contact: {
            lastname: d.lastName,
            firstname: d.firstName,
            address: d.address,
            phone: d.telephones && d.telephones.map(p => ({'@type': p.type || 'default', '#text': p.phone})),
            email: d.email,
            company: d.company,
            commentary: d.commentary,
        }
    }

    return create(element)
}

const readXml = async(data) => {

    const arrayIfNot = elem => Array.isArray(elem) ? elem : [elem]

    return new Promise((resolve, reject) => {
        parseString(data, {strict: true, async: true, explicitArray: false}, (err, result) => {
            console.log('DATA', result, err);

            if(err !== null) return reject(err)
            const { homebook } = result

            let ret = {
                contacts: [],
                homeData: {},
                equipments: [],
                diagnoses: [],
                maintenances: [],
                works: []
            }

            //1. extract contacts
            if(homebook.contacts !== "") {
                const contacts = arrayIfNot(homebook.contacts.contact)
                for(let c of contacts) {
                    let telephones = []
                    if(c.phone !== "") {
                        const phones = arrayIfNot(c.phone)
                        telephones = phones.map(p => ({type: p.$ && p.$.type, phone: p._}))
                    }
                    ret.contacts.push({
                        lastName: c.lastname,
                        firstName: c.firstname,
                        address: c.address,
                        commentary: c.commentary,
                        email: c.email,
                        company: c.company,
                        telephones
                    })
                }
            }

            //2. extract home data
            let telephones = []
            if(ret.homeData.phone !== "") {
                const phones = arrayIfNot(ret.homeData.phone)
                telephones = phones.map(p => ({type: p.$ && p.$.type, phone: p._}))
            }

            ret.homeData = {
                type: d.type,
                status: d.status,
                constructionDate: d.constructionDate,
                address: d.address,
                email: d.email,
                livingArea: d.livingArea,
                roomsCount: d.roomsCount,
                guarantees: d.guarantees.guarantee,
                rooms: d.rooms.room,
                contract: d.contract,
                telephones
            }
            //3. extract equipments
            if(homebook.equipments !== "") {
                const eqs = arrayIfNot(homebook.equipments.equipment)
                ret.equipments = [...eqs]
            }
            //4. extract Diagnoses
            if(homebook.diagnoses !== "") {
                const dias = arrayIfNot(homebook.diagnoses.diagnosis)
                ret.diagnoses = [...dias]
            }
            //5. extract Maintenances
            if (homebook.maintenances !== "") {
                const maints = arrayIfNot(homebook.maintenances.maintenance)
                ret.maintenances = [...maints]
            }
            //6. extract Works
            if (homebook.works !== "") {
                const works = arrayIfNot(homebook.works.work)
                ret.works = works.map(d => ({
                    type : d.type,
                    description : d.description,
                    roomNames : d.roomNames.roomName,
                    equipmentNames : d.equipmentNames.equipmentName,
                    craftsman : d.craftsman,
                    cost : d.cost,
                    dateStart : d.dateStart,
                    dateEnd : d.dateEnd,
                    contract : d.conract,
                    satisfaction : d.satisfaction,
                    status : d.status,
                    commentary : d.commentary,
                    contract : d.contract
                }))
            }

            resolve(ret)
        })
    })

}

const moveFile = async(file, parent) => {
    console.log('Move', file.attributes.name, parent.attributes.name)

    try {
        return await cozy.client.files.updateAttributesById(file._id, {dir_id: parent._id})
    } catch (error) {
        if(error.status === 409) { //confict file already exist
            //Move each child
            if(file.attributes.type === "directory") {
                const fileMaj = await cozy.client.files.statById(file._id)
                const childs = fileMaj.relations('contents')
                const newDir = await cozy.client.files.statByPath(parent.attributes.path + '/' + file.attributes.name)
                for(let child of childs) {
                    await moveFile(child, newDir)
                }
            }
        } else {
            throw error
        }
    }

}


