import { combineReducers } from 'redux'

import {
    RECALLS_NEW,
    RECALLS_NEW_SUCCESS,
    RECALLS_NEW_FAILURE,
    RECALLS_REMOVE,
    RECALLS_REMOVE_SUCCESS,
    RECALLS_REMOVE_FAILURE,
    RECALLS_SAVE,
    RECALLS_SAVE_SUCCESS,
    RECALLS_SAVE_FAILURE,
    RECALLS_SELECT,
    LOAD_RECALLS,
    LOAD_RECALLS_SUCCESS,
    LOAD_RECALLS_FAILURE,
    RECALLS_RESTORE_SUCCESS
} from '../actions'

import { STATUS_DONE, STATUS_ERROR, STATUS_LOAD } from '../constants'

export const elements = (state=[], action) => {
    switch (action.type) {
        case LOAD_RECALLS_SUCCESS:
            return action.recalls
        case RECALLS_NEW_SUCCESS:
            return [...state, action.recall]
        case RECALLS_REMOVE_SUCCESS:
            return state.filter(f => action.ids.indexOf(f._id) === -1)
        case RECALLS_SAVE_SUCCESS:
            const withoutRecall = state.filter(f => f._id !== action.recall._id)
            return [...withoutRecall, action.recall]
        default:
            return state
    }
}

export const selected = (state=[], action) => {
    switch (action.type) {
        case RECALLS_SELECT:
            if(state.indexOf(action.recall._id) === -1)
                return [...state, action.recall._id]
            else 
                return state.filter(f => f !== action.recall._id)
        default:
            return state
    }
}

const elementCount = (state = null, action) => {
    switch (action.type) {
        case LOAD_RECALLS_SUCCESS:
            return action.recalls.length
        case RECALLS_NEW_SUCCESS:
            return state + 1
        case RECALLS_REMOVE_SUCCESS:
            return state - action.ids.length
        case RECALLS_RESTORE_SUCCESS:
            return state + action.ids.length
        default:
            return state
    }
}

const fetchStatus = (state = STATUS_LOAD, action) => {
    switch (action.type) {
        case LOAD_RECALLS: 
        case RECALLS_REMOVE:
        case RECALLS_SAVE:
        case RECALLS_NEW:
            return STATUS_LOAD
        case LOAD_RECALLS_SUCCESS:
        case RECALLS_NEW_SUCCESS:
        case RECALLS_REMOVE_SUCCESS:
        case RECALLS_SAVE_SUCCESS:
            return STATUS_DONE
        case LOAD_RECALLS_FAILURE:
            return STATUS_ERROR
        default:
            return state
    }
}

export default combineReducers({
    elements,
    selected,
    elementCount,
    fetchStatus
})


