import _ from 'lodash'

import { ROOT_PATH, DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS } from '../constants'

import { getDataElements } from '../tools'

import { getFileTypes } from '../scripts/fileTypes'
const fileType = getFileTypes().map(f => f.key)

export const GAUGE_INITIALIZE = "GAUGE_INITIALIZE"
export const GAUGE_INITIALIZE_SUCCESS = "GAUGE_INITIALIZE_SUCCESS"
export const GAUGE_INITIALIZE_FAILURE = "GAUGE_INITIALIZE_FAILURE"

export const gaugeInitialize = () => {
    return async(dispatch, getState) => {
        dispatch({type: GAUGE_INITIALIZE})

        try {
            const types = await detectUsedTypesV2()

            const data = []
            await detectCreatedData(data)
            return dispatch({type: GAUGE_INITIALIZE_SUCCESS, types, data})
        } catch (err) {
            return dispatch({type: GAUGE_INITIALIZE_FAILURE, err})
        }
    }
}

