const detectUsedTypesV2 = async(types) => {
    const indexRef = await cozy.client.data.defineIndex('io.cozy.files', ['_id', 'type'])

    const allFiles = await cozy.client.data.query(indexRef, {
        "selector": {
            "_id": {
                $gt: null
            },
            "type": 'file'
        }
    })
    const sorted = allFiles.map(f => f.tags && f.tags.length > 0 && f.tags[0]).filter(f => !!f)
    const counted = _.countBy(sorted)

    const ret = _.map(counted, (count, type) => ({
        type,
        count
    }))
    return ret
}

const extractType = type => {
    if(fileType.indexOf(type) !== 0) return type
    return false
}

const detectCreatedData = async(data) => {

    const doctypes = [DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS]

    for(const doctype of doctypes) {
        const eles = await getDataElements(doctype)
        if(eles && eles.length > 0){
            data.push({doctype, count: eles.length})
        }
    }
} 


