import { COZY_ROOT_ID } from '../constants'

const TEMPFILE_PATH = '/tmp'

export const exist = async(name) => {
    try {
        return await cozy.client.files.statByPath(TEMPFILE_PATH + '/' + name)
    } catch (e) {
        return false
    }
}

export const deleteById = async(id) => {
    try {
        await cozy.client.files.trashById(id)
        await cozy.client.files.destroyById(id)
        return true
    } catch (e) {
        return false
    }
}

export const deleteIfExist = async(name) => {
    try {
        const file = await cozy.client.files.statByPath(TEMPFILE_PATH + '/' + name)
        await cozy.client.files.trashById(file._id)
        await cozy.client.files.destroyById(file._id)
        return true
    } catch (e) {
        return false
    }
}

export const create = async(data, name) => {
    await deleteIfExist(name)

    const dir = await getTemp()
    let ret = await cozy.client.files.create(data, {
        name,
        dirID: dir._id
    })
    ret.path = TEMPFILE_PATH + '/' + name
    return ret
}

export const createFolder = async(name) => {
    const dir = await getTemp()

    await deleteIfExist(name)

    return await cozy
        .client
        .files
        .createDirectory({name, dirID: dir._id})
}

export const getTemp = async() => {
    let temp
    try {
        temp = await cozy.client.files.statByPath(TEMPFILE_PATH)

    } catch (error) {
        temp = await cozy.client.files.createDirectory({
            name: "tmp",
            dirID: COZY_ROOT_ID,
        })
    }
    return temp
}



