import { ROOT_NAME, ROOT_PATH } from '../constants'

export const extractFileAttributes = f => Object.assign({}, f.attributes, {
    id: f._id,
    links: f.links
})

export const isDirectory = f => f && f.type && f.type === 'directory'

export const promiseTimeout = time => {
    return new Promise((resolve, reject) => setTimeout(resolve, time))
}

export const exctractReelPath = (path='/') => {
    if(path.indexOf('/') !== 0) path = '/'+path

    const folders = path.split('/')
    if(folders.length <= 1) {
        return ROOT_PATH + '/' + path
    } else {
        const join = folders.splice(2).join('/')
        return ROOT_PATH + (join.length > 1 ? '/'+join : '')
    }
}

export const createFakePath = (path='/') => {
    if(path.indexOf('/') !== 0) path = '/'+path

    const folders = path.split('/')
    if(folders.length <= 1) {
        return ROOT_NAME + '/' + path
    } else {
        const join = folders.splice(2).join('/')
        return ROOT_NAME + (join.length > 1 ? '/'+join : '')
    }
}

export const findParentPath = (path) => {
    if(path) {
        const folders = path.split('/')
        if(folders.length > 2)
            return folders.slice(0, folders.length-1).join('/')
    }
}

export const createOptionsFromArray = (array) => {
    return array.map((item, key) => (
        <option key={key} value={item}>{item}</option>
    ))
}

export const forceFileDownload = (href, filename) => {
    const element = document.createElement('a')
    element.setAttribute('href', href)
    element.setAttribute('download', filename)
    element.style.display = 'none'
    document.body.appendChild(element)
    element.click()
    document.body.removeChild(element)
}

export const getDataElements = async(doctype, addDoctypeInElements = false) => {
    const indexRef = await cozy
        .client
        .data
        .defineIndex(doctype, ['_id'])
    return await cozy
        .client
        .data
        .query(indexRef, {
            selector: {
                "_id": {
                    "$gt": null
                }
            }
        })
        .then(list => {
            if(addDoctypeInElements) return list.map(e => ({...e, doctype}))
            return list
        })
}

export const searchFilesName = async(query) => {
    const indexRef = await cozy.client.data.defineIndex('io.cozy.files', ['_id', 'name'])

    const res = await  cozy.client.data.query(indexRef, {
        "selector": {
            "_id": {
                $gt: null
            },
            "name": {
                $regex: `(?i)${query}`
            }
        }
    })

    return res
}

export const searchFilesTag = async(query) => {
    const indexRef = await cozy.client.data.defineIndex('io.cozy.files', ['_id', 'tags'])

    const res = await  cozy.client.data.query(indexRef, {
        "selector": {
            "_id": {
                $gt: null
            },
            "tags": {
                $in: [query]
            }
        }
    })

    return res
}


