export const DATA_REMOVE = "DATA_REMOVE"
export const DATA_REMOVE_SUCCESS = "DATA_REMOVE_SUCCESS"
export const DATA_REMOVE_FAILURE = "DATA_REMOVE_FAILURE"
export const DATA_NEW = "DATA_NEW"
export const DATA_NEW_SUCCESS = "DATA_NEW_SUCCESS"
export const DATA_NEW_FAILURE = "DATA_NEW_FAILURE"
export const DATA_SAVE = "DATA_SAVE"
export const DATA_SAVE_SUCCESS = "DATA_SAVE_SUCCESS"
export const DATA_SAVE_FAILURE = "DATA_SAVE_FAILURE"
export const DATA_RESTORE = "DATA_RESTORE"
export const DATA_RESTORE_SUCCESS = "DATA_RESTORE_SUCCESS"
export const DATA_RESTORE_FAILURE = "DATA_RESTORE_FAILURE"
export const DATA_SELECT = "DATA_SELECT"
export const DATA_LOAD = "DATA_LOAD"
export const DATA_LOAD_SUCCESS = "DATA_LOAD_SUCCESS"
export const DATA_LOAD_FAILURE = "DATA_LOAD_FAILURE"

export const dataSelect = data => ({
    type: DATA_SELECT, data
})

export const dataRemove = (doctype, data) => {
    return async(dispatch, getState) => {
        dispatch({type: DATA_REMOVE, data, doctype})

        try {
            const removed = []
            for(const d of data) {
                removed.push(await cozy.client.data.updateAttributes(doctype, d._id, {trash: true}))
            }
            return dispatch({type: DATA_REMOVE_SUCCESS, ids: removed.map(f => f._id), doctype})
        } catch (err) {
            return dispatch({type: DATA_REMOVE_FAILURE, err})
        }
    }
}

export const dataRestore = data => {
    return async(dispatch, getState) => {
        dispatch({type: DATA_RESTORE, data})
        try {
            const restored = []
            for(const d of data) {
                restored.push(await cozy.client.data.updateAttributes(d.doctype, d._id, {trash: false}))
            }
            return dispatch({type: DATA_RESTORE_SUCCESS, ids: restored.map(f => f._id), doctypes: restored.map(f => f._type)})
        } catch (err) {
            return dispatch({type: DATA_RESTORE_FAILURE, err})            
        }

    }
}

export const dataNew = (doctype, data) => {
    return async(dispatch, getState) => {
        dispatch({type: DATA_NEW, data, doctype})

        try {
            const newData = await cozy.client.data.create(doctype, data)
            return dispatch({type: DATA_NEW_SUCCESS, data: newData, doctype})
        } catch (err) {
            return dispatch({type: DATA_NEW_FAILURE, err})
        }
    }
}

export const dataSave = (doctype, id, data) => {
    return async(dispatch, getState) => {
        dispatch({type: DATA_SAVE, id, data, doctype})

        try {
            const newData = await cozy.client.data.updateAttributes(doctype, id, data)
            return dispatch({type: DATA_SAVE_SUCCESS, data: newData, doctype})
        } catch (err) {
            return dispatch({type: DATA_SAVE_FAILURE, err})
        }
    }
}

export const dataLoad = (doctype, id) => {
    return async(dispatch, getState) => {
        dispatch({type: DATA_LOAD, id, doctype})

        try {
            const data = await cozy.client.data.find(doctype, id)
            return dispatch({type: DATA_LOAD_SUCCESS, data, doctype})
        } catch (err) {
            return dispatch({type: DATA_LOAD_FAILURE, err})
        }
    }
}


