import React, {Component} from 'react'
import classNames from 'classnames'

import Loader from '../elements/Loader'

export default class TXTViewer extends Component {

    constructor(props, context) {
        super(props, context)

        this.state = {
            content: '',
            loading: true
        }
    }

    componentWillMount() {

        //load data
        fetch(this.props.url)
        .then(response => response.text())
        .then(data => this.setState({content: data, loading: false}))
        .catch(err => this.setState({content: 'Impossible de lire le fichier', loading: false}))
    }

    render() {
        return (
            <div>
                <Loader display={this.state.loading}/>
                {!this.state.loading && (
                    <div style={{textAlign: 'left', margin: '30px'}}>
                        {this.state.content.split('\n').map((item, key) => (<p key={key}>{item}</p>))}
                    </div>
                )}
                {!this.state.loading && this.state.error && (
                    <p style={{textAlign: 'center'}}>
                        {this.props.url}
                    </p>
                )}
            </div>
        )
    }
}


