import React, {Component} from 'react'
import classNames from 'classnames'
import moment from 'moment'
import Accordion from 'react-bootstrap/lib/Accordion'
import Panel from 'react-bootstrap/lib/Panel'

import button from '../../styles/buttons.scss'
import style from '../../styles/help.sass'
import _ from 'lodash'

import Page from './Page'
import Loader from './Loader'

import { VIEW_LIST, STATUS_LOAD, STATUS_DONE } from '../../constants'

export default class Contacts extends Component{

    constructor(props, context) {
        super(props, context)
    }

    componentWillMount () {
        this.reload()
    }

    componentWillReceiveProps (newProps) {
        // newProps.onLoadNews()
    }

    reload() {
        this.props.onLoadNews()
    }

    createHeader(item) {
        return (
            <span className={style.header}>
                <h3 className={style.title}>{item.title || "Notification"}</h3>
                <span>
                    <span>{item.category && item.category.name}</span>
                    <span> &#8226; </span>
                    <span>{moment(item.createdAt).format('LL')}</span>
                </span>
            </span>
        )
    }

    render() {
        const { news, count, status } = this.props

        const loading = status === STATUS_LOAD

        const reload = (<button onClick={() => this.reload()} className={classNames(button.button, button.default)}><i className="ion-ios-refresh-outline"></i>ACTUALISER</button>)
        const actions = [reload]

        return (
            <Page title="Actualités" subtitle={count !== null && (count > 0 ? (count > 1 ? `${count} actualités` : `1 actualité`) : 'Aucune actualité')}
            actions={actions}>
                <Loader display={loading}/>

                <div>
                    {!loading && news.length > 0 && news.map((item, key) => (
                        <Panel header={this.createHeader(item)} defaultExpanded collapsible eventKey={key} className={style.tab} key={key}>
                            <p dangerouslySetInnerHTML={html(item.content)}/>
                        </Panel>
                    ))}
                </div>

            </Page>
        )
    }
}

const html = text => {
  return {__html: text};
}



