import React, {Component} from 'react'
import classNames from 'classnames'
import style from '../../styles/DataTable.sass'
import button from '../../styles/buttons.scss'
import moment from 'moment'
import _ from 'lodash'

import CustomInput from '../utils/CustomInput'

export const DataTableGroup = (props) => {
    return (
        <div>
            <div className={style.row}>
                <h2>{props.title || ' '}</h2>
                {props.onAdd && props.displayAdd !== false && (<button onClick={e => {e.preventDefault(); props.onAdd(e)}} className={classNames(button.button, button.stable)} title="Ajouter">
                    <i className="ion-ios-plus-empty"></i>AJOUTER
                </button>)}
            </div>
            <table className={style.group}>
                <tbody>{props.children}</tbody>
            </table>
        </div>
    )
}

export const DataTableItem = props => {

    const renderValue = () => {
        if(props.updatable) return

        if(_.isObject(props.value)) {
            return _.map(props.value, (item, key) => (<div key={key}>{item}</div>))
        } else {
            let value = props.value
            switch (props.type) {
                case 'boolean':
                    value = value === 1 ? 'Oui' : 'Non'
                    break
            }
            return value
        } 
    }

    return (
        <tr className={style.item}>
            <th>
                <div>{props.title}</div>
            </th>
            <td>
                {props.updatable && (<CustomInput value={props.value} values={props.values} placeholder={props.title} type={props.type} onChange={props.onChange}/>)}
                {renderValue()}
            </td>
        </tr>
    )
}

export const DataTable = (props) => {
    return (
        <form className={style.main} onSubmit={() => props.onSubmit && props.onSubmit()}>
            {props.children}
        </form>
    )
}

DataTable.Item = DataTableItem
DataTable.Group = DataTableGroup

export default DataTable


