import { DOCTYPE_RECALLS } from '../constants'
import { trigger as createTrigger, deleteTrigger } from '../scripts/cozyRecalls'

export const RECALLS_CALENDAR_CLICK = "RECALLS_CALENDAR_CLICK"
export const RECALLS_NEW = "RECALLS_NEW"
export const RECALLS_NEW_SUCCESS = "RECALLS_NEW_SUCCESS"
export const RECALLS_NEW_FAILURE = "RECALLS_NEW_FAILURE"
export const RECALLS_SAVE = "RECALLS_SAVE"
export const RECALLS_SAVE_SUCCESS = "RECALLS_SAVE_SUCCESS"
export const RECALLS_SAVE_FAILURE = "RECALLS_SAVE_FAILURE"
export const RECALLS_REMOVE = "RECALLS_REMOVE"
export const RECALLS_REMOVE_SUCCESS = "RECALLS_REMOVE_SUCCESS"
export const RECALLS_REMOVE_FAILURE = "RECALLS_REMOVE_FAILURE"

export const recallsCalendarClick = id => ({type: RECALLS_CALENDAR_CLICK, id})

export const recallsRemove = recalls => {
    return async(dispatch, getState) => {
        dispatch({type: RECALLS_REMOVE, recalls})
        try {
            const removed = []
            for(const recall of recalls) {
                await deleteTrigger(recall.triggerId)
                removed.push(await cozy.client.data.delete(DOCTYPE_RECALLS, recall))
            }
            return dispatch({type: RECALLS_REMOVE_SUCCESS, ids: removed.map(f => f.id)})
        } catch (err) {
            return dispatch({type: RECALLS_REMOVE_FAILURE, err})
        }
    }
}

export const recallsNew = (name, data) => {
    return async(dispatch, getState) => {
        dispatch({type: RECALLS_NEW, name})

        try {
            const trigger = await createTrigger({date: data.alert || data.deadline, recallName: data.name, deadline: data.deadline})
            data.triggerId = trigger.data.id
            const recall = await cozy.client.data.create(DOCTYPE_RECALLS, data)
            return dispatch({type: RECALLS_NEW_SUCCESS, recall})
        } catch (err) {
            return dispatch({type: RECALLS_NEW_FAILURE, err})
        }
    }
}

export const recallsSave = (id, data) => {

    return async(dispatch, getState) => {
        dispatch({type: RECALLS_SAVE, id})

        try {
            await deleteTrigger(data.triggerId)
            const newTriggerId = await createTrigger({date: data.alert || data.deadline, recallName: data.name, deadline: data.deadline})
            data.triggerId = newTriggerId.data.id

            const recall = await cozy.client.data.updateAttributes(DOCTYPE_RECALLS, id, data)
            return dispatch({type: RECALLS_SAVE_SUCCESS, recall})
        } catch (err) {
            return dispatch({type: RECALLS_SAVE_FAILURE, err})
        }
    }
}


