import { DOCUMENTS_CHANGE_VIEW_MODE, TRASH_CHANGE_VIEW_MODE } from '../actions'

import { VIEW_GRID, VIEW_LIST } from '../constants'

export const viewMode = (state = VIEW_LIST, action) => {
  switch (action.type) {
    case DOCUMENTS_CHANGE_VIEW_MODE:
    case TRASH_CHANGE_VIEW_MODE:
        if(state === VIEW_GRID)
            return VIEW_LIST
        else
            return VIEW_GRID
    default:
      return state
  }
}

export default viewMode


