import { autoImport } from './AutoImport'
import { InitKonnector } from './InitKonnector'

import { alertShow, loadFolder } from '../actions'
import { ROOT_PATH, ALERT_LEVEL_ERROR } from '../constants'

const initScript = async(alert) => {
    let needReload = false

    try {
        const status = await autoImport(alert)
        console.log('Auto import OK', status)
        if(status === 1) {
            alert('Importation automatique de données terminé')
            needReload = true
        }
    } catch (err) {
        alert('Erreur lors de l\'importation automatique', ALERT_LEVEL_ERROR)
        console.error('Auto import fail', err)
    }

    try {
        const code = await InitKonnector()
        console.log('Initialize Konnector', code)

    } catch (err) {
        console.error('Initialize Konnector fail', err)
    }

    return needReload
}

export default initScript


