import {
    combineReducers
} from 'redux'

import {
    LOAD_NEWS,
    LOAD_NEWS_SUCCESS,
    LOAD_NEWS_FAILURE
} from '../actions'

import {
    DOCTYPE_NEWS,
    STATUS_LOAD,
    STATUS_DONE,
    STATUS_ERROR
} from '../constants'

export const elements = (state=[], action) => {
    switch (action.type) {
        case LOAD_NEWS_SUCCESS:
            return action.news
        default:
            return state
    }
}

export const elementCount = (state = null, action) => {
    switch (action.type) {
        case LOAD_NEWS_SUCCESS:
            return action.news.length
        default:
            return state
    }
}

export const fetchStatus = (state = STATUS_LOAD, action) => {
    switch (action.type) {
        case LOAD_NEWS:
            return STATUS_LOAD
        case LOAD_NEWS_SUCCESS:
            return STATUS_DONE
        case LOAD_NEWS_FAILURE:
            return STATUS_ERROR
        default:
            return state
    }
}

export default combineReducers({
    elements,
    elementCount,
    fetchStatus
})



