import { IMPORTER_FILE_UPLOAD_SUCESS,
    GAUGE_INITIALIZE_SUCCESS,
    GAUGE_INITIALIZE_FAILURE,
    GAUGE_INITIALIZE,
    DOCUMENTS_REMOVE_SUCCESS,
    DOCUMENTS_RESTORE_SUCCESS,
    DATA_REMOVE_SUCCESS,
    DATA_RESTORE_SUCCESS } from '../actions'

import { STATUS_DONE, STATUS_ERROR, STATUS_LOAD } from '../constants'

const defaultState = {
    usedTypes: [],
    dataCreated: [],
    status: STATUS_LOAD
}

export const gauge = (state = defaultState, action) => {
    switch (action.type) {
        case IMPORTER_FILE_UPLOAD_SUCESS:
            const types = []
            for(let file of action.files) {
                const type = getTypeOf(file)

                if(type === undefined) continue

                let find = false
                for(let t of state.usedTypes) {
                    if(t.type === type) {
                        t.count = t.count + 1 || 1
                        find = true
                        break
                    }
                }
                if(!find) {
                    types.push({type, count:1})
                }
            }

            return {...state, usedTypes: [...state.usedTypes, ...types]}

        case DOCUMENTS_RESTORE_SUCCESS:
            const types2 = []
            for(let type of action.types) {

                let find = false
                for(let t of state.usedTypes) {
                    if(t.type === type) {
                        t.count = t.count + 1 || 1
                        find = true
                        break
                    }
                }
                if(!find) {
                    types2.push({type, count:1})
                }
            }
            return {...state, usedTypes: [...state.usedTypes, ...types2]}

        case DOCUMENTS_REMOVE_SUCCESS:
            for(let type of action.types) {

                for(let t of state.usedTypes) {
                    if(t.type === type) {
                        t.count = t.count - 1
                        if (t.count <= 0) {
                            const index = state.usedTypes.indexOf(t)
                            state.usedTypes.splice(index, 1)
                        }
                        break
                    }
                }
            }
            return {...state, usedTypes: [...state.usedTypes]}

        case DATA_RESTORE_SUCCESS:
            let data = []
            for(let doctype of action.doctypes) {
                let find = false
                for(let t of state.dataCreated) {
                    if(t.doctype === doctype) {
                        t.count = t.count + 1 || 1
                        find = true
                        break
                    }
                }
                if(!find) {
                    data.push({doctype, count: 1})
                }
            }
            return {...state, dataCreated: [...state.dataCreated, ...data]}

        case DATA_REMOVE_SUCCESS:
            const doctype = action.doctype
            const count = action.ids.length

            for(let t of state.dataCreated) {
                if(t.doctype === doctype) {
                    t.count = t.count - count
                    if (t.count <= 0) {
                        const index = state.dataCreated.indexOf(t)
                        state.dataCreated.splice(index, 1)
                    }
                    break
                }
            }
            return {...state, dataCreated: [...state.dataCreated]}

        case GAUGE_INITIALIZE:
            return {...state, status: STATUS_LOAD}
        case GAUGE_INITIALIZE_SUCCESS:
            return {...state, usedTypes: action.types, dataCreated: action.data, status: STATUS_DONE}
        case GAUGE_INITIALIZE_FAILURE:
            return {...state, status: STATUS_ERROR}
        default:
            return state
    }
}

export default gauge

const getTypeOf = file => {
    return file.tags && file.tags[0];
}


