import React, {Component} from 'react'
import classNames from 'classnames'
import moment from 'moment'
import style from '../../styles/ElementList.sass'

import {VIEW_GRID, VIEW_LIST} from '../../constants'

export const ContactList = (props) => {

    return (
        <div className={classNames(style.list, {
            [style.viewList]: props.view === VIEW_LIST,
            [style.viewGrid]: props.view === VIEW_GRID
        })}>
            {props.children.map((item, key) => {
                let title = item.company
                if(item.lastName && item.firstName) {
                    title = item.firstName + " " + item.lastName
                } else if(item.lastName) {
                    title = item.lastName
                } else if(item.lastName) {
                    title = item.firstName
                }

                return (
                    <div key={key} onClick={e => props.onClickItem(item, e, key)} className={classNames(style.document, {[style.selected]: item.selected})}>
                        {props.onSelectItem && item.selected && (
                            <i className={classNames(style.checkbox, "ion-android-checkbox-outline")} onClick={(e) => {e.stopPropagation(); props.onSelectItem(item, e, key, false);}}></i>
                        )}

                        {props.onSelectItem && !item.selected && (
                            <i className={classNames(style.checkbox, "ion-android-checkbox-outline-blank")} onClick={(e) => {e.stopPropagation(); props.onSelectItem(item, e, key, true);}}></i>
                        )}

                        <div className={style.content}>
                            <img
                                src={item.selected ? "/images/contact.selected.png" : "/images/contact.png"}
                                onError={e => console.log('Error icon')}
                            ></img>
                        </div>
                        <div className={style.name} title={title}>
                            <span>{title}</span>
                        </div>
                        <div
                        className={style.contact}
                        title={item.email}>
                            {item.telephones && item.telephones[0] && (<span>Tel : {item.telephones[0].phone}</span>)}
                            {item.type && (<span>Spec : {item.type}</span>)}
                            {item.email && (<span>@ : {item.email}</span>)}
                        </div>
                    </div> 
                )
            })}
        </div>
    )
}

export default ContactList


