import {
    DOCTYPE_CONTACTS,
    DOCTYPE_DIAGNOSIS,
    DOCTYPE_EQUIPMENTS,
    DOCTYPE_HOMEDATA,
    DOCTYPE_MAINTENANCES,
    DOCTYPE_META,
    DOCTYPE_NEWS,
    DOCTYPE_RECALLS,
    DOCTYPE_WORKS,
    ROOT_PATH
} from '../constants'

import { getDataElements } from '../tools'

export const resetHomebook = async() => {

    try {
        //Delete all data
        await deleteAll(DOCTYPE_CONTACTS)
        await deleteAll(DOCTYPE_DIAGNOSIS)
        await deleteAll(DOCTYPE_EQUIPMENTS)
        await deleteAll(DOCTYPE_HOMEDATA)
        await deleteAll(DOCTYPE_MAINTENANCES)
        await deleteAll(DOCTYPE_META)
        await deleteAll(DOCTYPE_NEWS)
        await deleteAll(DOCTYPE_RECALLS)
        await deleteAll(DOCTYPE_WORKS)

        //Delete Homebook dir
        const homebookDir = await cozy.client.files.statByPath(ROOT_PATH)
        await cozy.client.files.trashById(homebookDir._id)
        await cozy.client.files.clearTrash()

    } catch (error) {
        console.error('Reset Homebook', error)
    }
}

const deleteAll = async(doctype) => {
    const data = await getDataElements(doctype)
    for(const d of data) {
        await cozy.client.data.delete(doctype, d)
    }
}


