import React, {Component} from 'react'
import classNames from 'classnames'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'
import moment from 'moment'

import frequencies from '../../constants/recallsFrequencies.json'
import {createOptionsFromArray} from '../../tools'
import button from '../../styles/buttons.scss'
import style from '../../styles/ContactRecallModal.sass'

export default class ContactModal extends Component {
    constructor(props, context) {
        super(props, context)

        const item = props.item || {}

        this.state = {
            name: item.name || '',
            deadline: item.deadline || moment(props.defaultDate).format('DD/MM/YYYY'),
            alert: item.alert,
            frequency: item.frequency || '',
            notes: item.notes || '',
            edit: false
        }
    }

    componentWillMount() {
        if (this.props.edit === true) 
            this.state.edit = true
    }

    componentWillReceiveProps(newProps) {}

    save(e) {
        //create new object
        const {name, deadline, alert, frequency, notes} = this.state

        const newRecall = {
            name,
            deadline,
            alert,
            frequency,
            notes
        }

        this
            .props
            .onSave(newRecall)
        this.close(e)
    }

    remove(e) {
        this
            .props
            .onRemove()
        this.close(e)
    }

    edit(e) {
        e.stopPropagation()

        this.setState({edit: true})
    }

    close(e) {
        e.stopPropagation()
        //close the portal
        this
            .props
            .closePortal()
    }

    renderEdition() {
        return (
            <div className={style.edition} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                    <h1>{this.props.title}</h1>

                    <button
                        className={classNames(button.button, button.default)}
                        onClick={(e) => this.save(e)}>
                        <i className="fa fa-floppy-o"></i>
                        Enregistrer
                    </button>

                    <button
                        onClick={e => this.close(e)}
                        className={classNames(button.button, button.stable, button.clear)}>
                        <i className="ion-ios-close-empty"></i>
                    </button>
                </div>
                <form className={style.content}>
                    <div className={style.group}>
                        <label>Nom</label>
                        <input
                            type="text"
                            value={this.state.name}
                            onChange={e => this.setState({name: e.target.value})}
                            className="form-control"/>
                    </div>

                    <div className={style.group}>
                        <label>Echéance</label>
                        <DatePicker
                            dateFormat="DD/MM/YYYY"
                            locale="fr-fr"
                            isClearable={false}
                            selected={moment(this.state.deadline, 'DD/MM/YYYY')}
                            onChange={e => this.setState({
                            deadline: e && e.format('DD/MM/YYYY')
                        })}/>
                    </div>

                    <div className={style.group}>
                        <label>Alerte</label>
                        <DatePicker
                            dateFormat="DD/MM/YYYY"
                            locale="fr-fr"
                            isClearable={true}
                            selected={this.state.alert && moment(this.state.alert, 'DD/MM/YYYY')}
                            onChange={e => this.setState({
                            alert: e && e.format('DD/MM/YYYY')
                        })}/>
                    </div>

                    <div className={style.group}>
                        <label>Fréquence</label>
                        <select
                            type="text"
                            value={this.state.frequency}
                            onChange={(e) => this.setState({frequency: e.target.value})}
                            className="form-control">
                            <option>Aucune</option>
                            {createOptionsFromArray(frequencies)}

                        </select>
                    </div>
                    
                    <div className={style.group}>
                        <label>Notes</label>
                        <textarea
                            value={this.state.notes}
                            onChange={e => this.setState({notes: e.target.value})}
                            className="form-control"/>
                    </div>
                </form>
            </div>
        )
    }

    renderView() {
        return (
            <div className={style.main} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                    <h1>{this.state.name}</h1>

                    <button
                        className={classNames(button.button, button.default)}
                        onClick={(e) => this.edit(e)}>
                        <i className="fa fa-edit"></i>
                        <span className={button.text}>Modifier</span>
                    </button>

                    <button
                        onClick={e => this.remove(e)}
                        className={classNames(button.button, button.assertive)}>
                        <i className="ion-ios-trash-outline"></i>
                        <span className={button.text}>Supprimer</span>
                    </button>

                    <button
                        onClick={e => this.close(e)}
                        className={classNames(button.button, button.stable, button.clear)}>
                        <i className="ion-ios-close-empty"></i>
                    </button>
                </div>
                <div className={style.content}>
                    <div>
                        <h3>Echéance</h3>
                        <p>{this.state.deadline}</p>
                    </div>

                    <div>
                        <h3>Alerte</h3>
                        <p>{this.state.alert}</p>
                    </div>

                    <div>
                        <h3>Fréquence</h3>
                        <p>{this.state.frequency}</p>
                    </div>

                    <div>
                        <h3>Note</h3>
                        <div>
                            {this.state.notes && this
                                .state
                                .notes
                                .split('\n')
                                .map((item, key) => (
                                    <div key={key}>{item}</div>
                                ))}
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    render() {
        if (this.state.edit) 
            return this.renderEdition()
        else 
            return this.renderView()
    }

}


