import React, {Component} from 'react'
import style from './styles/App.sass'
import button from './styles/buttons.scss'
import classNames from 'classnames'

import createHistory from 'history/createHashHistory'
// import createHistory from 'history/createBrowserHistory'
import { Router } from 'react-router'
import { Route, IndexRoute, Switch, Redirect} from 'react-router-dom'
import Portal from 'react-portal'

import { ROOT_PATH } from './constants'

//Piwik tracker
import { shouldEnableTracking, getTracker } from './tracker'

// import SearchBar from './components/SearchBar'
import Navigation from './containers/Navigation'
import Main from './components/elements/Main'
import Footer from './components/elements/Footer'
import Page from './components/elements/Page'

import Onboarding from './components/elements/Onboarding'
import Documents from './containers/Documents'
import Contacts from './containers/Contacts'
import Recalls from './containers/Recalls'
import Trash from './containers/Trash'
import Data from './containers/Data'
import Assistant from './containers/Assistant'
import DataForm from './containers/DataForm'
import News from './containers/News'
import Dashboard from './components/elements/Dashboard'
import TaskList from './containers/TaskList'

import Preferences from './components/elements/Preferences'
import Help from './components/elements/Help'
import About from './components/elements/About'

import Alerter from './containers/Alerter'

import initScript from './scripts/initial'

const App = ( props ) => {

    let history = createHistory()

    if (shouldEnableTracking() && getTracker()) {
        let trackerInstance = getTracker()
        history = trackerInstance.connectToHistory(history)
        trackerInstance.track(history.location.pathname) // when using a hash history, the initial visit is not tracked by piwik react router
    }

    cozy.client.files.statByPath(ROOT_PATH)
    .then(() => {
        props.initilizeDocs()
    })
    .catch(err => {
        console.log('No route file')
        props.initilizeDocs()
    })

    initScript(props.alert)
    .then(needReload => {
        console.log('Init script done reload:', needReload)
        if(needReload) {
            props.reloadDocs()
        }
    })

    return (
        <Router history={history}>
            <div className={style.app}>

                {/*<SearchBar/>*/}

                <div className={style.container}>
                    <Navigation/>
                    <div className={style.page}>
                        <Main>
                            <Route exact path="/" component={Onboarding}/>
                            <Route path="/accueil" component={Dashboard}/>                            
                            <Route path="/Documents/:path*" component={Documents}/>
                            <Route path="/data/:slug/:id" component={Data}/>
                            <Route path="/contacts/:id?" component={Contacts}/>
                            <Route path="/rappels" component={Recalls}/>
                            <Route path="/corbeille" component={Trash}/>
                            <Route exact path="/assistant" component={Assistant}/>
                            <Route path="/assistant/:slug" component={DataForm}/>
                            <Route path="/du-nouveau/:id?" component={TaskList}/>

                            <Route path="/actualites" component={News}/>
                            <Route path="/preferences" component={Preferences}/>
                            <Route path="/aide" component={Help}/>
                            <Route path="/a-propos" component={About}/>
                            <Footer/>
                        </Main>
                    </div>
                </div>

                <Alerter/>

            </div>
        </Router>
    )
}



export default App



